import React,{useState, useEffect, useRef} from 'react';
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Link from '@material-ui/core/Link';
import Select from '@material-ui/core/Select';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import ShareOutlinedIcon  from '@material-ui/icons/ShareOutlined';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import GitHubIcon from '@material-ui/icons/GitHub';
import EventOutlinedIcon from '@material-ui/icons/EventOutlined';
import Avatar from '@material-ui/core/Avatar';
import AvatarGroup from '@material-ui/lab/AvatarGroup';
import Divider from '@material-ui/core/Divider';
import Tooltip from '@material-ui/core/Tooltip';
import './DailyScrum.scss';
import { useHistory, useParams } from 'react-router-dom';
import Drawer from '@material-ui/core/Drawer';
import environment from '../../config/environment';
import ContentEditable from 'react-contenteditable'

const axios = require('axios');
function DailyScrum(props){
    const {project_id} = useParams();
    const text = useRef('')
    const [anchorElMember, setAnchorElMember] = React.useState(null);
    const openMember = Boolean(anchorElMember);
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [option, changeOption] = useState(10);
    const [sprintOption, changeSprintOption] = useState("");
    const history = useHistory();
    const handleCloseMember = () => {
        setAnchorElMember(null);
    };
    const openTeamMember = (event) => {
        setAnchorElMember(event.currentTarget);
    };
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };
    const handleChange = (event) => {
        changeOption(event.target.value);
    };
    
    const handleRedirectDashboard = () => {
        history.push("/")
    }

    const handleTextChange = evt => {
        text.current = evt.target.value;
    };
     
    const handleBlur = () => {
        console.log(text.current);
    };
    

    useEffect(() => {
        if (props.token) {
            axios.post(environment.url + '/user/info', { token: props.token })
                .then(response => {
                    if (response.status === 200) {
                        props.checkLoggedIn(true);
                        props.updateUser(response.data.data.user);
                        if (response.data.data.user.steps <= 2) {
                            props.updateStep(response.data.data.user.steps)
                            history.push("/onboarding");
                        }
                    }
                })
                .catch((error) => {
                    props.checkLoggedIn(false);
                    history.push('/login');
                });
        }else{
            history.push('/login')
        }
    }, [props.token]);
    return (
        <React.Fragment>
          
            <Container maxWidth={"xl"} className="sprint-container">
                <Box display="flex" flexDirection="row">
                    <Box display="flex" className="project-board">
                        <Box className="project-board__left">
                        <Breadcrumbs aria-label="breadcrumb">
                            <Link color="inherit" underline="always" onClick={handleRedirectDashboard}>Dashboard</Link>
                            {/* <Link color="inherit" href="/getting-started/installation/" onClick={handleClick}>Product Backlog</Link> */}
                            <Typography color="textPrimary">Product Backlog</Typography>
                        </Breadcrumbs>
                            
                            <Box elevation={0}  display="flex" className="repo">
                                <Box component="span" className="ttl has-background" >
                                    <Typography variant="body2" noWrap={false}>Intelligaia</Typography>
                                </Box>
                                <Box elevation={0} display="flex" flexDirection="row" alignItems="center" className="repo__name connected">
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        value={option}
                                        onChange={handleChange}
                                        label="Age"
                                    >
                                        <MenuItem value={10}><GitHubIcon /></MenuItem>
                                        <MenuItem value={20} disabled><img src="/images/gitlab.svg"/></MenuItem>
                                        <MenuItem value={30} disabled><img src="/images/jira.svg"/></MenuItem>
                                    </Select>
                                    <Link href="#"  className="g-name d-none">Connect To Github</Link>
                                    <Link href="#" className="g-name" >Add Repository from GIT</Link>
                                </Box>
                                <Box elevation={0} display="flex" flexDirection="row" alignItems="center"  className="repo__utility">
                                    <Tooltip title="Share" placement="top" arrow>
                                        <IconButton aria-label="Share" size="small" > 
                                            <ShareOutlinedIcon  fontSize="small" />
                                        </IconButton> 
                                    </Tooltip>
                                    <Tooltip title="More Actions" placement="top" arrow>
                                        <IconButton aria-label="Action" size="small" aria-controls="actionMenu" aria-haspopup="true" onClick={handleClick} > 
                                            <MoreVertIcon fontSize="small" />
                                        </IconButton>
                                    </Tooltip>
                                    <Menu
                                        id="actionMenu"
                                        anchorEl={anchorEl}
                                        keepMounted
                                        open={Boolean(anchorEl)}
                                        onClose={handleClose}
                                    >
                                        <MenuItem onClick={handleClose}>Manage Team Members</MenuItem>
                                        <Divider />
                                        <MenuItem onClick={handleClose} disabled>Remove Repository</MenuItem>
                                        <MenuItem onClick={handleClose}><Box className="textDanger" component="span">Delete Project</Box></MenuItem>
                                    </Menu>
                                    
                                </Box>
                            </Box>
                            <Box>
                            </Box>
                            <Box elevation={0} className="avatar d-none">
                                <AvatarGroup max={3}>
                                    <Avatar alt="Remy Sharp" src="https://material-ui.com/static/images/avatar/1.jpg"  />
                                    <Avatar alt="Travis Howard" src="https://material-ui.com/static/images/avatar/2.jpg" />
                                    <Avatar alt="Cindy Baker" src="https://material-ui.com/static/images/avatar/3.jpg" />
                                </AvatarGroup>
                                <Link href="#" className="g-team" onClick={openTeamMember}>(3) Team Members</Link>
                                <Menu
                                    id="teamMemberMenu"
                                    anchorEl={anchorElMember}
                                    keepMounted
                                    open={Boolean(openMember)}
                                    onClose={handleCloseMember}
                                >
                                    <MenuItem><Box className="MuiTypography-h6">Team Members (3)</Box></MenuItem>
                                    <MenuItem onClick={handleCloseMember}>
                                        <Box display="block">
                                            <Typography variant="subtitle2">Daniel Webster</Typography>
                                            <Box className="MuiTypography-caption">danial@intelligaia.com</Box>
                                        </Box>
                                    </MenuItem>
                                    <Divider />
                                    <MenuItem onClick={handleCloseMember}>
                                        <Box display="block">
                                            <Typography variant="subtitle2">Derek Barnhouse</Typography>
                                            <Box className="MuiTypography-caption">derek@intelligaia.com</Box>
                                        </Box>
                                    </MenuItem>
                                    <Divider />
                                    <MenuItem onClick={handleCloseMember}>
                                        <Box display="block">
                                            <Typography variant="subtitle2">John Miller</Typography>
                                            <Box className="MuiTypography-caption">jmiller@intelligaia.com</Box>
                                        </Box>
                                    </MenuItem>
                                </Menu>
                            </Box> 
                        </Box>
                        <Box className="project-board__rgt" flexGrow={1}>
                            <Box display="flex" className="sprint">
                                <Box className="timeline active sprint__backlog" style={{ width: '25%' }}>
                                    <Typography variant="caption" className="sprint__label bold600">Product Backlog</Typography>
                                    <Typography variant="subtitle2" className="sprint__label bold500">1</Typography>
                                </Box>
                                <Box className="timeline active sprint__plan" style={{ width: '30%' }}>
                                    <Typography variant="caption" className="sprint__label bold600">Plan Sprint</Typography>
                                    <Typography variant="subtitle2" className="sprint__label bold500">2-Week Sprint</Typography>
                                    {/* <Box className="sprint-form">
                                        <FormControl >
                                            <Select value={sprintOption} onChange={handleSprintChange} displayEmpty value={''} inputProps={{ 'aria-label': 'Without label' }} >
                                                <MenuItem value="">
                                                    <em>Select Sprint</em>
                                                </MenuItem>
                                                <MenuItem value={10} className={(sprintOption!==10 && sprintOption!=="")?classes.hasCheck:""}> {sprintOption===10?<CheckIcon></CheckIcon>:<></>}1-Week Sprint</MenuItem>
                                                <MenuItem value={20} className={(sprintOption!==20 && sprintOption!=="")?classes.hasCheck:""}> {sprintOption===20?<CheckIcon></CheckIcon>:<></>}2-Week Sprint</MenuItem>
                                                <MenuItem value={30} className={(sprintOption!==30 && sprintOption!=="")?classes.hasCheck:""}> {sprintOption===30?<CheckIcon></CheckIcon>:<></>}4-Week Sprint</MenuItem>
                                            </Select>
                                        </FormControl>
                                        <Datepicker/>
                                        <Button variant="contained" size="small" color="primary" href="#contained-buttons">Go</Button>
                                    </Box> */}
                                </Box>
                                <Box className="timeline active sprint__scrum" style={{ width: '15%' }}>
                                    <Typography variant="caption" className="sprint__label bold600">Daily Scrum</Typography>                                    
                                    <Typography variant="subtitle2" className="sprint__label bold500">10:00 am for 15 min</Typography>
                                </Box>
                                <Box className="timeline sprint__review" style={{ width: '15%' }}>
                                    <Typography variant="caption" className="sprint__label bold600">Sprint Review</Typography>
                                </Box>
                                <Box className="timeline sprint__retro" style={{ width: '15%' }}>
                                    <Typography variant="caption" className="sprint__label bold600">Sprint Retrospective</Typography>
                                </Box>
                            </Box>
                        </Box>
                    </Box>
                </Box>   
            </Container> 
            <Container maxWidth={"xl"} className="product-container">
              
                 <Box display="flex" width="100%" className="ds">
                    <Box p={2} flexGrow={1} className="ds__left">
                        {/* Daily Notes Header */}
                        <Box display="flex" alignItems="center">
                            <Box  width="70%"   flexGrow={1} className="backlog__title">
                                {/* <Typography variant="h4">Daily Scrum Notes <Box component="span" className="MuiTypography-subtitle2">19 Oct, 2020</Box></Typography>  */}
                                <Typography variant="h4">Daily Scrum Notes</Typography>
                                <FormControl fullWidth className="d-none">  {/* fullWidth for full width  */}
                                    <TextField required id="standard-required" label="" width="100%" defaultValue="19 Oct, 2020" />
                                </FormControl>
                            </Box>                            
                            <Box width="30%" className="action-btns" display="flex" justifyContent="flex-end" alignItems="center">
                                <Box  className="avatar">
                                    <AvatarGroup max={3}>
                                        <Avatar alt="Remy Sharp" src="https://material-ui.com/static/images/avatar/1.jpg" />
                                        <Avatar alt="Travis Howard" src="https://material-ui.com/static/images/avatar/2.jpg" />
                                        <Avatar alt="Cindy Baker" src="https://material-ui.com/static/images/avatar/3.jpg" />
                                    </AvatarGroup>
                                </Box>
                                <Box className="share-btn" ml={1}>
                                    <Button variant="outlined" color="primary" size="small" startIcon={<ShareOutlinedIcon />}>Share</Button>
                                </Box>
                            </Box>
                            
                        </Box>
                        <Box display="block" width="100%" className="userStory">
                            {/*<FormControl fullWidth />  */}  {/* fullWidth for full width  */}
                            <Box component="div" display="flex" width="100%" my={1}>
                                <TextField
                                    id="standard-multiline-flexible"
                                    label=""
                                    multiline
                                    // rowsMax={20}
                                    style={{width:"100%"}} 
                                    onChange={handleChange}
                                    defaultValue="19 Feb 2021"
                                    placeholder="Enter your notes title here."
                                /> 
                            </Box>
                             <Box component="div" display="flex" width="100%" my={2}>
                                <Typography variant="h5">19 Feb 2021 </Typography>
                             </Box>
                             
                        </Box>
                        <ContentEditable html={text.current} onBlur={handleBlur} onChange={handleTextChange}  className="contentEditor" />
                        <Box display="block" borderRadius={6} width="30%" className="preview" my={2}>
                            <Box display="block" mb={2.5} className="preview__header">
                                <Typography variant="h5" gutterBottom>Suggested Scrum Template</Typography>
                                <Typography variant="caption" gutterBottom>You can add scrum template</Typography>
                            </Box>
                            <Box display="block" mb={2.5} className="preview__body">
                                <Typography variant="h6">What did you do yesterday? </Typography>
                                <Box className="scrum-field" my={1}>
                                    <TextField
                                        id="yesterdayTask"
                                        label=""
                                        multiline
                                       // rowsMax={4}
                                        placeholder="Enter your yesterday's task here..."
                                        onChange={handleChange}
                                    />
                                </Box>
                                <Typography variant="h6">What will you do today?</Typography>
                                <Box className="scrum-field" my={1}>
                                    <TextField
                                        id="todayTask"
                                        label=""
                                        multiline
                                       // rowsMax={4}
                                        placeholder="Enter your Today's task here..."
                                        onChange={handleChange}
                                    />
                                </Box>
                                <Typography variant="h6">Where are you blocked? </Typography>
                                <Box className="scrum-field" my={1}>
                                    <TextField
                                        id="taskBlocker"
                                        label=""
                                        multiline
                                       // rowsMax={4}
                                        placeholder="Enter your blockers here..."
                                        onChange={handleChange}
                                    />
                                </Box>
                                <Typography variant="h6">Comfort Level — How close are we to hitting our sprint goals</Typography>
                                <Box className="scrum-field" my={1}>
                                    <TextField
                                        id="sprintGoal"
                                        label=""
                                        multiline
                                       // rowsMax={4}
                                        placeholder="Enter your sprint goals level here..."
                                        onChange={handleChange}
                                    />
                                </Box>
                            </Box> 
                            <Box display="block" className="preview__footer">
                                <Divider className="scrum-divider" />
                                <Box display="flex" justifyContent="center" mt={2}>
                                    <Button variant="outlined" color="primary">Add</Button>
                                </Box>
                            </Box>
                        </Box>
                    </Box>
                    <Box p={2} className="ds__rgt">
                        <Drawer variant="permanent" >
                            <Box component="div" display="block"  textAlign="center" py={1}>
                                <IconButton aria-label="calendar" >
                                    <EventOutlinedIcon fontSize="small" />
                                </IconButton>
                                <Box component="div" textAlign="center"  mt={.5} className="MuiTypography-caption">Calendar</Box>
                            </Box>
                             <Box component="div" display="block"  textAlign="center" py={1}>
                                <IconButton aria-label="edit-note" >
                                    <img src="/images/edit-note.svg" alt="Scrumbooster"></img>
                                </IconButton>
                                <Box component="div" textAlign="center" mt={.5} className="MuiTypography-caption">Notes</Box>
                            </Box>
                            <Box component="div" display="block"  textAlign="center" py={1}>
                                <IconButton aria-label="template" >
                                    <img src="/images/template.svg" alt="Scrumbooster"></img>
                                </IconButton>
                                <Box component="div" textAlign="center" mt={.5} className="MuiTypography-caption">Templates</Box>
                            </Box>
                        </Drawer>
                    </Box>
                </Box>
                
            </Container>
        </React.Fragment>
    );
}

 export default DailyScrum;