import React, { useState, useEffect } from 'react';
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Link from '@material-ui/core/Link';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Select from '@material-ui/core/Select';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import IconButton from '@material-ui/core/IconButton';
import ShareOutlinedIcon from '@material-ui/icons/ShareOutlined';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import CreateIcon from '@material-ui/icons/Create';
import GitHubIcon from '@material-ui/icons/GitHub';
import SortIcon from '@material-ui/icons/Sort';
import LaunchIcon from '@material-ui/icons/Launch';
import Avatar from '@material-ui/core/Avatar';
import AvatarGroup from '@material-ui/lab/AvatarGroup';
import Divider from '@material-ui/core/Divider';
import TextField from '@material-ui/core/TextField';
import Datepicker from '../Datepicker';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import ChatOutlinedIcon from '@material-ui/icons/ChatOutlined';
import FilterListIcon from '@material-ui/icons/FilterList';
import CreateOutlinedIcon from '@material-ui/icons/CreateOutlined';
import Tooltip from '@material-ui/core/Tooltip';
import Dialogbox from '../Dialogbox';
import Collapse from '@material-ui/core/Collapse';
import Share from '../Share';
import './Dashboard.scss';
import { useHistory } from 'react-router-dom';
import environment from '../../config/environment';
import Message from '../Core/Message';
import DoneIcon from '@material-ui/icons/Done';
import Input from '@material-ui/core/Input';
import InputAdornment from '@material-ui/core/InputAdornment';
import InputBase from '@material-ui/core/InputBase';
import { positions } from '@material-ui/system';

const axios = require('axios');

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

function Dashboard(props) {
    const [open, setOpen] = useState(true);
    const [shareProject, changeShareProject] = useState(false);
    const [sprint, changeSprint] = useState("");
    const [currentStep, changeStep] = useState(0);
    const [dialogOpen, changeDialog] = useState(false);
    const [anchorElMember, setAnchorElMember] = useState(null);
    const openMember = Boolean(anchorElMember);
    const [messageopen, setMessageOpen] = useState(false);
    const [content, setContent] = useState({ type: "success", message: "" });
    const [projects, changeProjects] = useState([]);
    const [repoList, changeRepoList] = useState([]);
    const [selectedProject, changeSelectedProject] = useState(null);
    const handleMessageClose = () => {
        setMessageOpen(false);
        setContent({ type: "success", message: "" });
    }

    const [anchorEl, setAnchorEl] = useState(null);
    const history = useHistory();
    const handleChangeSprint = (event) => {
        changeSprint(event.target.value);
    }

    const handleUpdateStep = (val) => {
        changeStep(val);
    }

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleUpdateProjectRepo = (data) => {
        var tempProject = [...projects];
        tempProject.forEach((value, index) => {
            if (data.project_id === value.project_id) {
                tempProject[index] = data.data;
                tempProject[index]['editable'] = false;
            }
        })
        changeProjects(tempProject);
    }

    const handleUpdateMembers = (data) =>{
        var tempProject = [...projects];
        tempProject.forEach((value, index) => {
            if (data.project_id === value.project_id) {
                tempProject[index]['members'] = data.members;
            }
        })
        changeProjects(tempProject);
    }

    const handleSuccess = (message) =>{
        setContent({ type: "success", message: message });
        setMessageOpen(true);
        setTimeout(function () { setMessageOpen(false); }, 2000);
    }

    const handleClose = () => {
        setAnchorEl(null);
    };



    const handleCloseMember = () => {
        setAnchorElMember(null);
    };
    const openTeamMember = (event) => {
        setAnchorElMember(event.currentTarget);
    };
    const [option, changeOption] = useState(10);
    const handleDialogClose = () => {
        changeDialog(false);
        changeStep(0);
        changeSelectedProject(null);
    }
    const handleDialogOpen = (project, index) => {
        // console.log('project',project);
        // if(project.repository_id){
        //     axios.get(environment.url + '/project/'+ project.project_id)
        //         .then(response => {
        //             if (response.status === 200) {
        //                 // console.log(response);
        //                 // var tempProject = [...projects];
        //                 // tempProject[index] = response.data.data.project
        //                 changeSelectedProject(response.data.data.project);
        //                 console.log('selected project=>',response.data.data.project);
        //                 // changeProjects(tempProject);
        //                 changeDialog(true);
        //             }
        //         })
        //         .catch((error) => {
        //             setContent({ type: "error", message: error.response.data.message });
        //             setMessageOpen(true);
        //             setTimeout(function () { setMessageOpen(false); }, 2000);
        //         });
        // }else{
        //     changeDialog(true);
        //     changeSelectedProject(project);
        // }
        changeDialog(true);
        changeSelectedProject(project);
    }
    const handleChange = (event) => {
        changeOption(event.target.value);
    };

    const redirectToBacklog = () => {
        history.push('/backlog');
    }

    const handleShareProject = () => {
        changeShareProject(true);
    }

    const closeShareProject = () => {
        changeShareProject(false);
    }

    const handleEditProject = (index) => {
        let tempProject = [...projects]
        tempProject[index]['editable'] = true;
        changeProjects(tempProject);
    }

    const handleChangeTitle = (index, event) => {
        var tempProject = [...projects];
        tempProject[index]['title'] = event.target.value;
        changeProjects(tempProject);
    }

    const handleDailyScrum = (id) =>{
        history.push('/daily-scrum?project='+id)
    }

    const handleUpdateProject = (index, project_id) => {
        if (props.token) {
            axios.put(environment.url + '/project/update', { project_id: project_id, title: projects[index]['title'], token: props.token })
                .then(response => {
                    if (response.status === 200) {
                        var tempProject = [...projects];
                        tempProject[index]['editable'] = false;
                        changeProjects(tempProject);
                    }
                })
                .catch((error) => {
                    setContent({ type: "error", message: error.response.data.message });
                    setMessageOpen(true);
                    setTimeout(function () { setMessageOpen(false); }, 2000);
                });
        }
    }

    const handleAddProject = () => {
        if (props.token) {
            axios.post(environment.url + '/project/create', { title: "Sample Project " + (projects.length + 1), token: props.token })
                .then(response => {
                    if (response.status === 201) {
                        var tempProjects = [...projects];
                        let project = { ...response.data.data.project };
                        project['editable'] = false;
                        tempProjects.unshift(project);
                        changeProjects(tempProjects);
                    }
                })
                .catch((error) => {
                    setContent({ type: "error", message: error.response.data.message });
                    setMessageOpen(true);
                    setTimeout(function () { setMessageOpen(false); }, 2000);
                });
        }
    }

    const handleDeleteProject = (event) => {
        console.log('delete project', event.currentTarget.value);
    }

    const rediretToKanban = () =>{
        history.push("/kanban")
    }

    useEffect(() => {
        if (props.token) {
            axios.post(environment.url + '/project/list', { token: props.token })
                .then(response => {
                    if (response.status === 200) {
                        let projects = [...response.data.data.projects];
                        projects.forEach(value => {
                            value.editable = false;
                        })
                        changeProjects(projects);
                    }
                })
                .catch((error) => {
                    setContent({ type: "error", message: error.response.data.message });
                    setMessageOpen(true);
                    setTimeout(function () { setMessageOpen(false); }, 2000);
                });
        }
    }, [props.token]);

    useEffect(() => {
        if (props.user.github_connected) {
            axios.post(environment.url + '/github/repository-list', { token: props.token })
                .then(response => {
                    if (response.status === 200) {
                        changeRepoList(response.data.data.repositories)
                    }
                })
                .catch((error) => {
                    setContent({ type: "error", message: error.response.data.message });
                    setMessageOpen(true);
                    setTimeout(function () { setMessageOpen(false); }, 2000);
                });
        }
    }, [props.user])

    return (
        <React.Fragment>
            <Box display="block" className="dashboard-outr">
                <Box display="block" className="dashboard-main" component="div">
                    <Grid item xs={12} className="dashboardTitle">

                        <Typography variant="h3" className="regular400" gutterBottom>Get going with sample <Box fontWeight="fontWeightBold" component="span">Project</Box> and related <Box fontWeight="fontWeightBold" component="span">Sprints</Box></Typography>

                        <Typography variant="h3" className="regular400 d-none" gutterBottom>This <Box fontWeight="fontWeightBold" component="span"><Link href="#" color="primary">week</Link></Box> has <Box fontWeight="fontWeightBold" component="span">1 Project</Box> and <Box fontWeight="fontWeightBold" component="span">0 Sprint</Box></Typography>
                    </Grid>
                    <Grid item xs={12} container direction="row" justify="center" alignItems="stretch" className="kpiCards-main">
                        <Grid item xs={5} container justify="space-between"
                            alignItems="stretch">
                            <Grid item xs={8} container className="kpi-cards kpi-cards-first">
                                <Box border={1} borderColor="grey.500" borderRadius={6}>
                                    <Box display="flex" justifyContent="space-between" component="div">
                                        <Typography variant="h5">Total Running Sprints</Typography>
                                        <Typography variant="h4">0</Typography>
                                    </Box>
                                    <Box display="flex" justifyContent="space-between" component="div">
                                        <Box component="div">
                                            <Typography variant="caption">Upcoming Sprints</Typography>
                                            <br></br>
                                            <Typography variant="h6">0</Typography>
                                        </Box>
                                        <Box component="div">
                                            <Typography variant="caption">Off - Track Sprints</Typography>
                                            <br></br>
                                            <Typography variant="h6">0</Typography>
                                        </Box>
                                    </Box>
                                </Box>
                            </Grid>
                            <Grid item xs={4} container className="kpi-cards">
                                <Box component="div" border={1} borderColor="grey.500" borderRadius={6}>
                                    <Typography variant="subtitle-2">Total Scrums</Typography>
                                    <br></br>
                                    <Typography variant="h6">0</Typography>
                                </Box>
                            </Grid>
                        </Grid>
                        <Grid item xs={7} container justify="space-between"
                            alignItems="stretch">
                            <Grid item xs={8} container className="kpi-cards">
                                <Box display="flex" border={1} borderColor="grey.500" borderRadius={6} className="noPadding">
                                    <Box borderRight={1} component="div" className="border-color" p={1} style={{ width: '30%' }}>
                                        <Typography variant="subtitle-2">Team Participation</Typography>
                                        <br></br>
                                        <Typography variant="h6"> 0 </Typography>
                                    </Box>
                                    <Box component="div" p={1} style={{ width: '70%' }}>
                                        <Typography variant="subtitle-2">Team Capacity</Typography>
                                        <br></br>
                                        <Typography variant="caption"><strong>0%</strong></Typography>
                                    </Box>
                                </Box>
                            </Grid>
                            <Grid item xs={4} container className="kpi-cards quick-card">
                                <Box border={1} borderColor="grey.500" borderRadius={6}>
                                    <Box display="flex" justify="space-between" component="div" className="inner">
                                        <Typography variant="subtitle-2">Quick Templates</Typography>
                                        <Tooltip title="Open in new window" placement="top-end" arrow>
                                            <IconButton type="button" fontSize="small" className="expand-icon"><LaunchIcon fontSize="small" /></IconButton>
                                        </Tooltip>
                                    </Box>

                                    <Box display="flex" justify="space-between" mt={0}>
                                        <Avatar>DS</Avatar>
                                        <Box component="div" ml={2} mr={1} textOverflow="ellipsis" overflow="hidden" className="text-more"><Typography variant="caption">Speed up Sprint Planning with Smart Templates like Daily Scrum, Retrospective and 7 more...</Typography></Box>
                                    </Box>
                                </Box>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12} container direction="row" justify="space-between" alignItems="center" className="kpiButtons">
                        <Button variant="text" className="filterButton" size="small">
                            Filters
                            <IconButton type="button" aria-label="Filters" fontSize="small"><FilterListIcon fontSize="small" /></IconButton>
                        </Button>
                        <Button variant="contained" size="small" onClick={handleAddProject}>
                            <IconButton type="button" aria-label="Add Project" className="icon-plus"></IconButton>Add Project
                    </Button>
                    </Grid>
                </Box>
                {projects.map((value, index) => {
                    return <Box display="flex" flexDirection="row" key={index}>
                        <Box display="flex" className="project-board">
                            <Box className="project-board__left">
                                <Box component="div" className="ttl" >
                                    {/* <Box position="relative" top={0} bgcolor="green" >
                                        <Box position="relative" top={0} component="div" display="inline" maxWidth="100%"  pr="2rem" textOverflow="ellipsis" overflow="hidden" whiteSpace="nowrap" boxSizing="border-box" bgcolor="orange" className="project-ttl">
                                            Sample Project Sample Project Sample Project Sample Project Sample Project Sample Project
                                            <Box position="absolute" top={0} right={0}>
                                                <IconButton aria-label="Edit" size="small" onClick={() => handleEditProject(index)}>
                                                    <CreateOutlinedIcon />
                                                </IconButton>
                                            </Box>    
                                        </Box>
                                    </Box> */}

                                    <Box className={"nonEdit" + (!value.editable ? "" : " d-none")} display="flex" mb={1} mr={1}>
                                        <InputBase value={value.title} readOnly endAdornment={<Tooltip title="Edit" placement="top" arrow>
                                            <IconButton aria-label="Edit" size="small" onClick={() => handleEditProject(index)}>
                                                <CreateOutlinedIcon />
                                            </IconButton>
                                        </Tooltip>} />
                                        {/* <div style={{ width: '90%', whiteSpace: 'nowrap' }}>
                                            <Box component="div"
                                                textOverflow="ellipsis"
                                                overflow="hidden">
                                                {value.title}
                                            </Box>
                                        </div>
                                        <Tooltip title="Edit" placement="top" arrow>
                                            <IconButton aria-label="Edit" size="small" onClick={()=>handleEditProject(index)}>
                                                <CreateOutlinedIcon />
                                            </IconButton>
                                        </Tooltip> */}
                                    </Box>
                                    <Box className={"editable" + (value.editable ? "" : " d-none")} display="flex" mb={1} mr={1}>
                                        <InputBase value={value.title} onChange={(event) => handleChangeTitle(index, event)} autoFocus={true}/>
                                        <Tooltip title="Done" placement="top" arrow>
                                            <IconButton onClick={() => handleUpdateProject(index, value.project_id)}><DoneIcon color="primary" /></IconButton>
                                        </Tooltip>
                                    </Box>
                                </Box>
                                <Box elevation={0} display="flex" className="repo">
                                    <Box elevation={0} display="flex" flexDirection="row" alignItems="center" className="repo__name connected">
                                        {/* <GitHubIcon fontSize="small" />
                                        <IconButton aria-label="DropDownIcon" size="small">
                                            <ArrowDropDownIcon fontSize="small" />
                                        </IconButton>  */}
                                        <Box className="selectCheck">
                                            <Select
                                                labelId="demo-simple-select-outlined-label"
                                                id="demo-simple-select-outlined"
                                                value={option}
                                                onChange={handleChange}
                                                label="Age"
                                            >
                                                <MenuItem value={10}><GitHubIcon /></MenuItem>
                                                <MenuItem value={20} disabled><img src="/images/gitlab.svg" /></MenuItem>
                                                <MenuItem value={30} disabled><img src="/images/jira.svg" /></MenuItem>
                                            </Select>
                                            <Box className={"selectCheckIcon" + (props?.user?.github_connected ? "" : " d-none")}><img src="/images/green-check.svg" /></Box>
                                        </Box>
                                        <Link href="#" className={"g-name" + (props?.user?.github_connected ? " d-none" : "")}>Connect To Github</Link>
                                        <Link href="#" underline="none" className={((value?.repository?.name) ? "repoName bold500" : "g-name") + (props?.user?.github_connected ? "" : " d-none")} onClick={() => handleDialogOpen(value, index)}>{(value?.repository?.name) ? (value?.repository?.name) : "Add Repository from GIT"}</Link>
                                    </Box>
                                    <Box elevation={0} display="flex" flexDirection="row" alignItems="center" className="repo__utility">

                                        <Tooltip title="Share" placement="top" arrow>
                                            <IconButton aria-label="Share" size="small" onClick={handleShareProject} >
                                                <ShareOutlinedIcon fontSize="small" />
                                            </IconButton>
                                        </Tooltip>
                                        <Tooltip title="More Actions" placement="top" arrow>
                                            <IconButton aria-label="Action" size="small" aria-controls="actionMenu" aria-haspopup="true" onClick={handleClick} >
                                                <MoreVertIcon fontSize="small" />
                                            </IconButton>
                                        </Tooltip>
                                        <Share open={shareProject} onClose={closeShareProject} />

                                        <Menu
                                            id="actionMenu"
                                            anchorEl={anchorEl}
                                            keepMounted
                                            open={Boolean(anchorEl)}
                                            onClose={handleClose}
                                        >
                                            <MenuItem onClick={handleClose}>Manage Team Members</MenuItem>
                                            <Divider />
                                            <MenuItem onClick={handleClose} disabled={true}>Remove Repository</MenuItem>
                                            <MenuItem value={value.project_id} onClick={handleDeleteProject}><Box className="textDanger" component="span">Delete Project</Box></MenuItem>
                                        </Menu>

                                    </Box>
                                </Box>
                                <Box>
                                </Box>
                                <Box elevation={0} className={"avatar" + (value?.repository ? "" : " d-none")}>
                                    <AvatarGroup max={3} className={value?.members.length>0?"": "d-none"}>
                                        {value?.members.map((member,index)=>{
                                            return <Avatar key={index} alt="Remy Sharp" src={member.avatar} />
                                        })}
                                    </AvatarGroup>
                                    <Link href="#" className={"g-team"+(value?.members.length>0?"": " d-none")} onClick={openTeamMember}><Box fontWeight="fontWeightBold" component="span">({value?.members.length})</Box> Team Members</Link>
                                    <Menu
                                        id="teamMemberMenu"
                                        anchorEl={anchorElMember}
                                        keepMounted
                                        open={Boolean(openMember)}
                                        onClose={handleCloseMember}
                                    >
                                        <MenuItem><Box className="MuiTypography-h6">Team Members (3)</Box></MenuItem>
                                        {value?.members.map((member,index)=>{
                                            return (
                                                <MenuItem onClick={handleCloseMember} key={index}>
                                                    <Divider className={index !== 0 ? "" : "d-none"} />
                                                    <Box display="block">
                                                        <Typography variant="subtitle2">{member.username}</Typography>
                                                        <Box className="MuiTypography-caption">{member.email}</Box>
                                                    </Box>
                                                </MenuItem>)
                                        })}
                                    </Menu>
                                </Box>
                            </Box>
                            <Box className="project-board__rgt" flexGrow={1}>
                                <Box display="flex" className="sprint">
                                    <Box className="timeline active sprint__backlog" style={{ width: '25%' }}>
                                        <Typography variant="caption" className="sprint__label bold600">Product Backlog</Typography>
                                        <Typography variant="body2">Total issues <Box fontWeight="fontWeightBold" component="span"><Link href="#">1</Link></Box></Typography>
                                        <Button variant="outlined" size="small" color="primary" onClick={redirectToBacklog}>Create Product Backlog</Button>
                                    </Box>
                                    <Box className="timeline active sprint__plan" style={{ width: '33%' }}>
                                        <Typography variant="caption" className="sprint__label bold600">Sprint Plan</Typography>
                                        <Typography variant="body2"><Link href="#">2-Week Sprint starts on <Box component="span" fontWeight="fontWeightMedium">Nov 26, 2020</Box></Link> <Box component="span" fontStyle="italic" className="grey-text">Current</Box></Typography>
                                        <Box className="sprint-form">
                                            <FormControl >
                                                <Select onChange={handleChangeSprint} displayEmpty value={sprint} inputProps={{ 'aria-label': 'Without label' }}>
                                                    <MenuItem value="">
                                                        <em>Select Sprint</em>
                                                    </MenuItem>
                                                    <MenuItem value={1}>1-Week Sprint</MenuItem>
                                                    <MenuItem value={2}>2-Week Sprint</MenuItem>
                                                    <MenuItem value={3}>3-Week Sprint</MenuItem>
                                                    <MenuItem value={4}>4-Week Sprint</MenuItem>
                                                </Select>
                                            </FormControl>
                                            <Datepicker />
                                            <Button variant="contained" size="small" color="primary" onClick={rediretToKanban}>Go</Button>
                                        </Box>
                                    </Box>
                                    <Box className="timeline active sprint__scrum" style={{ width: '15%' }}>
                                        <Typography variant="caption" className="sprint__label bold600">Daily Scrum</Typography>
                                        <Typography variant="body2" className="fontWeightMedium">10:00 am for 15 min</Typography>
                                        <Button variant="outlined" size="small" color="primary" onClick={()=>handleDailyScrum(value.project_id)}>Daily Scrum</Button>
                                    </Box>
                                    <Box className="timeline sprint__review" style={{ width: '15%' }}>
                                        <Typography variant="caption" className="sprint__label bold600">Sprint Review</Typography>
                                        <Typography variant="body2" className="fontWeightMedium">1 hrs</Typography>
                                    </Box>
                                    <Box className="timeline sprint__retro" style={{ width: '12%', paddingRight: '0' }}>
                                        <Typography variant="caption" className="sprint__label bold600">Sprint Retrospective</Typography>
                                        <Typography variant="body2" className="fontWeightMedium">45 min</Typography>
                                    </Box>
                                </Box>
                            </Box>
                        </Box>
                        <Box className="quickNotes">
                            <Card className="quickNote-card">
                                <CardContent>
                                    <Typography variant="caption">19 Oct, 2020</Typography>
                                    <TextField
                                        placeholder="|  Add quick note here"
                                        multiline
                                        rows={3}
                                        rowsMax={3}
                                        className="quickNote-textfield" />
                                </CardContent>
                                <CardActions>
                                    <Button size="small" color="primary">Go to Scrumboard</Button>
                                    <Button size="small" color="primary" className="d-none">Done</Button>
                                </CardActions>
                            </Card>
                        </Box>
                    </Box>
                })
                }

                <Box className="alerts toaster-alerts d-none">
                    {/* <Snackbar open={open} onClose={handleAlertClose}> */}
                    <Alert variant="outlined" severity="info" className="customAlert" icon={false}>
                        <Box display="flex" component="span"><ChatOutlinedIcon /> Is the process to plan the sprint helpful?</Box>
                        <Box component="span">
                            <Box component="span" mr={2}><Link href="javascript:void(0);">Yes</Link></Box>
                            <Box component="span"> <Link href="javascript:void(0);" >No</Link></Box>
                        </Box>
                    </Alert>
                    {/* </Snackbar> */}
                    <Collapse in={open}>
                        <Alert variant="outlined" severity="success" color="success" onClose={() => { setOpen(false); }}>You have successfully connected to GitHub</Alert>
                        <Alert variant="outlined" severity="success" color="success" onClose={() => { setOpen(false); }}>Repository added successfully with team members.</Alert>
                    </Collapse>
                </Box>
                <Dialogbox token={props.token} open={dialogOpen} repos={repoList} project={selectedProject} onClose={handleDialogClose} currentStep={currentStep} updateCurrentStep={handleUpdateStep} updateRepo={handleUpdateProjectRepo} updateMember={handleUpdateMembers} showSuccess={handleSuccess} />
            </Box>
            <Message open={messageopen} content={content} onClose={handleMessageClose} />
        </React.Fragment>
    );
}

export default Dashboard;