import { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import IconButton from '@material-ui/core/IconButton';
import Box from '@material-ui/core/Box';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import InputAdornment from '@material-ui/core/InputAdornment';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import VisibilityIcon from '@material-ui/icons/Visibility';
import Link from '@material-ui/core/Link';
import Icon from '@material-ui/core/Icon';
import Tooltip from '@material-ui/core/Tooltip';
import './SignUp.scss';
import Message from '../../Core/Message';
import FormHelperText from '@material-ui/core/FormHelperText';
import Input from '@material-ui/core/Input';
import environment from '../../../config/environment';
import validation from '../../../config/validation';
const axios = require('axios');
const icons = [{ icon: 'Microsoft', path: '/images/icon-micro.svg', height: "19px", width: "20px" }, { icon: 'Google', path: '/images/icon-google.svg', height: "20px", width: "20px" }, { icon: 'GitHub', path: '/images/icon-github.svg', height: "20px", width: "20px" }];
function SignUp(props) {
    const [visible, setVisible] = useState(false);//for password
    const [open, setOpen] = useState(false);
    const [content, setContent] = useState({ type: "success", message: "" });
    const [name, changeName] = useState("");
    const [email, changeEmail] = useState("");
    const [password, changePassword] = useState("");
    const [inputError, changeInputError] = useState({ name: { invalid: false, message: "" }, email: { invalid: false, message: "" }, password: { invalid: false, message: "" } })

    const handleChangeName = (event) => {
        changeName(event.target.value);
        var error = { ...inputError };
        error.name = { invalid: false, message: "" };
        if (event.target.value.trim() !== "") { changeInputError(error) }
    }
    const handleChangeEmail = (event) => {
        changeEmail(event.target.value);
        var error = { ...inputError };
        error.email = { invalid: false, message: "" };
        if (event.target.value.trim() !== "") { changeInputError(error) }
    }
    const handleChangePassword = (event) => {
        changePassword(event.target.value);
        var error = { ...inputError };
        error.password = { invalid: false, message: "" };
        if (event.target.value !== "") { changeInputError(error) }
    }
    const history = useHistory();
    const handleLogin = () => {
        history.push('/login')
    }

    const handleSignup = (event) => {
        event.preventDefault();
        var validateResponse = validation({
            email: email,
            name: name,
            password: password
        });

        if (validateResponse.invalid) {
            var errors = validateResponse.errors;
            changeInputError({
                name: { invalid: errors.name.invalid, message: errors.name.message },
                email: { invalid: errors.email.invalid, message: errors.email.message },
                password: { invalid: errors.password.invalid, message: errors.password.message }
            })
        }

        if (name.trim() !== "" && email.trim() !== "" && password.trim() !== "" && !validateResponse.invalid) {
            let data = { name: name.trim(), email: email.trim(), password: password }
            axios.post(environment.url + '/user/register', data)
                .then(response => {
                    if (response.status === 201) {
                        setOpen(true);
                        setContent({ type: "success", message: response.data.message });
                        setTimeout(function () { history.push('/activate-account?mail_sent=success'); }, 1500);
                    }
                })
                .catch((error) => {
                    setContent({ type: "error", message: error.response.data.message });
                    setOpen(true);
                });
        }
    }
    const handleClose = () => {
        setOpen(false);
    }
    const handleVisible = () => {
        setVisible(!visible);
    }

    useEffect(() => {
        if (props.token) {
            axios.post(environment.url + '/user/info', { token: props.token })
                .then(response => {
                    if (response.status === 200) {
                        props.checkLoggedIn(true);
                        props.updateUser(response.data.data.user);
                        if (response.data.data.user.steps <= 2) {
                            props.updateStep(response.data.data.user.steps)
                            history.push("/onboarding");
                        }else{
                            history.push("/");
                        }
                    }
                })
                .catch((error) => {
                    props.checkLoggedIn(false);
                    history.push('/login');
                });
        }
    }, [props.token]);
    return (
        <div className="form-container">
            <Container fixed >
                <Grid container spacing={3} maxWidth="sm">
                    <Grid item xs="12" align="center">
                        <Typography variant="h2">Get started absolutely free</Typography>
                        <Typography variant="body1">Free forever. No credit card needed.</Typography>

                        <Box display="block" component="div" className="loginForm">
                            <form onSubmit={handleSignup}>
                                <FormControl error={inputError.name.invalid}>
                                    <InputLabel shrink htmlFor="name">Name</InputLabel>
                                    <Input
                                        id="name"
                                        type="text"
                                        value={name}
                                        onChange={handleChangeName}
                                        placeholder="First Name Last Name"
                                        InputLabelProps={{ shrink: true }}
                                    />
                                    <FormHelperText id="component-error-text" className={inputError.name.invalid ? "" : "d-none"}>{inputError.name.message}</FormHelperText>
                                </FormControl>
                                <FormControl error={inputError.email.invalid}>
                                    <InputLabel shrink htmlFor="email">Email ID</InputLabel>
                                    <Input
                                        id="email"
                                        type="email"
                                        placeholder="Email"
                                        value={email}
                                        onChange={handleChangeEmail}
                                        InputLabelProps={{ shrink: true }}
                                    />
                                    <FormHelperText id="component-error-text" className={inputError.email.invalid ? "" : "d-none"}>{inputError.email.message}</FormHelperText>
                                </FormControl>
                                <FormControl error={inputError.password.invalid}>
                                    <InputLabel shrink htmlFor="password">Password</InputLabel>
                                    <Input
                                        id="password"
                                        type={visible ? "text" : "password"}
                                        placeholder="Password"
                                        InputLabelProps={{ shrink: true }}
                                        value={password}
                                        autoComplete="off"
                                        onChange={handleChangePassword}
                                        endAdornment={
                                            <InputAdornment position="end">
                                                <Tooltip title={visible ? "Hide Password" : "Show Password"} placement="top-end" arrow>
                                                    <IconButton aria-label="toggle password visibility" onClick={handleVisible} >
                                                        {visible ? <VisibilityIcon /> : <VisibilityOff />}
                                                    </IconButton>
                                                </Tooltip>
                                            </InputAdornment>
                                        }
                                    />
                                    <FormHelperText id="component-error-text" className={inputError.password.invalid ? "" : "d-none"}>{inputError.password.message}</FormHelperText>
                                </FormControl>
                                <Box mt={4} mb={3}>
                                    <Button type="submit" color="primary" variant="contained" fullWidth>Sign Up</Button>
                                </Box>
                            </form>
                            <Box mt={4} mb={3} mx='auto' className="authorizeWith" display="flex" alignItems="center" justifyContent="center" flexDirection="column">
                                <Box display="flex" alignItems="center" justifyContent="center">
                                    <Typography variant="overline" className="authorizeText">OR AUTHORIZE WITH</Typography>
                                </Box>
                                <Box display="flex" alignItems="center" justifyContent="space-between" className="authorizeBtn" mt={2} mb={3}>
                                    {icons.map(value => {
                                        return (<Button variant="outlined" startIcon={<Icon><img alt={value.icon} src={value.path} height={value.height} width={value.width} /></Icon>}>{value.icon}</Button>);
                                    })}
                                </Box>
                                <Box display="flex" alignItems="center" justifyContent="center">
                                    <Typography variant="caption">Already have an account? <Link component="button" variant="caption" onClick={handleLogin}>Log In</Link></Typography>
                                </Box>
                            </Box>
                        </Box>
                    </Grid>
                </Grid>
            </Container>
            <Message open={open} content={content} onClose={handleClose} />
        </div>
    )
}

export default SignUp;