const validEmailRegex = RegExp(/^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i);

function validation(object) {
    var errors = {};
    Object.entries(object).forEach(([key, value]) => {
        var field = { field: key, invalid: false, message: "" }
        var response = validateField(key, value, object);
        if (response !== "") {
            field.invalid = true;
            field.message = response;
        }
        errors[key] = field;
    })

    var invalid = false;
    Object.entries(errors).forEach(([key, value]) =>{
        if(value.invalid){ invalid= true; }
    })
    return {errors, invalid};
}

function validateField(field, value, object) {
    switch (field) {
        case "email":
            if (value.trim() === "") { return "Email cannot be empty"; }
            else if (!validEmailRegex.test(value)) { return "Email is not valid!"; }
            else { return ""; }

        case "name":
            if (value.trim() === "") { return "Name cannot be empty"; }
            else { return ""; }

        case "password":
            if (value.trim() === "") { return "Password cannot be empty"; }
            if (value.trim().length < 6) { return "The Password must be atleast 6 characters long"; }
            if (value.trim().length > 20) { return "The Password must be maximum 20 characters long"; }
            else { return ""; }

        case "confirm_password":
            if (value.trim() === "") { return "Confirm Password cannot be empty"; }
            if (value.trim().length < 6) { return "The Confirm Password must be atleast 6 characters long"; }
            if (value.trim().length > 20) { return "The Confirm Password must be maximum 20 characters long"; }
            if (object['password'] && value.trim() !== object['password']) { return "Password Do Not Match"; }
            else { return ""; }

        default:
            return "";
    }
}

export default validation;