import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import IconButton from '@material-ui/core/IconButton';
import Box from '@material-ui/core/Box';
import { withStyles } from '@material-ui/core/styles';
import InputBase from '@material-ui/core/InputBase';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import InputAdornment from '@material-ui/core/InputAdornment';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import VisibilityIcon from '@material-ui/icons/Visibility';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Icon from '@material-ui/core/Icon';
import Tooltip from '@material-ui/core/Tooltip';

import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';

import './ResetPassword.scss';

import Message from '../../Core/Message';
import FormHelperText from '@material-ui/core/FormHelperText';
import Input from '@material-ui/core/Input';
import environment from '../../../config/environment';
import validation from '../../../config/validation';
import {useEffect, useState} from 'react';
import { useHistory, useLocation } from 'react-router-dom';
const axios = require('axios');
function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const BootstrapInput = withStyles((theme) => ({}))(InputBase);
const icons = [{ icon: 'Microsoft', path: '/images/icon-micro.svg', height: "19px", width: "20px" }, { icon: 'Google', path: '/images/icon-google.svg', height: "20px", width: "20px" }, { icon: 'GitHub', path: '/images/icon-github.svg', height: "20px", width: "20px" }];
function useQuery() {
    return new URLSearchParams(useLocation().search);
}
function ResetPassword(props) {
    const history = useHistory();
    const [open, setOpen] = useState(false);
    const [content, setContent] = useState({ type: "success", message: "" });
    const [password, changePassword] = useState("");
    const [confirm_password, changeConfirmPassword] = useState("");
    const [passwordvisible, changePasswordVisible] = useState(false)
    const [confirmpasswordvisible, changeConfirmPasswordVisible] = useState(false)
    const [inputError, changeInputError] = useState({ password: { invalid: false, message: "" }, confirm_password: { invalid: false, message: "" } })
    const [loading, changeLoading] = useState(false);
    const query = useQuery();
    const email = query.get('email');
    const token = query.get('token');
    const handleClose = () =>{
        setOpen(false);
    }
    const handleChangePassword = (event) =>{
        changePassword(event.target.value);
        var error = { ...inputError };
        error.password = { invalid: false, message: "" };
        if (event.target.value.trim() !== "") { changeInputError(error) }
    }
    const handleChangeConfirmPassword = (event) =>{
        changeConfirmPassword(event.target.value);
        var error = { ...inputError };
        error.confirm_password = { invalid: false, message: "" };
        if (event.target.value.trim() !== "") { changeInputError(error) }
    }

    const handlePasswordVisible = () =>{
        changePasswordVisible(!passwordvisible);
    }

    const handleConfirmPasswordVisible = () =>{
        changeConfirmPasswordVisible(!confirmpasswordvisible);
    }

    const formSubmit = (event) =>{
        event.preventDefault();
        var validateResponse = validation({
            password: password,
            confirm_password: confirm_password,
        });

        if (validateResponse.invalid) {
            var errors = validateResponse.errors;
            changeInputError({
                password: { invalid: errors.password.invalid, message: errors.password.message },
                confirm_password: { invalid: errors.confirm_password.invalid, message: errors.confirm_password.message }
            })
        }

        if (password.trim() !== "" && confirm_password.trim() !== "" && !validateResponse.invalid && token && email) {
            changeLoading(true);
            axios.post(environment.url + '/user/reset-password', { email: email, password: password, token: token })
                .then(response => {
                    if (response.status === 200) {
                        setOpen(true);
                        setContent({ type: "success", message: response.data.message });
                        changePassword("");
                        changeConfirmPassword("");
                        changeLoading(false);
                        setTimeout(function () {
                            history.push("/login");
                        }, 2000);
                    }
                })
                .catch((error) => {
                    changeLoading(false);
                    setContent({ type: "error", message: error.response.data.message });
                    setOpen(true);
                });
        }
    }
    return (
        <div className="form-container">
            <Container fixed >
                <Grid container spacing={3} maxWidth="sm">
                    <Grid item xs="12" align="center">
                        <Typography variant="h2">Reset Password</Typography>
                        <Box className="form-desc"><Typography variant="body1">Create a Strong Password You’ll Never Forget</Typography></Box>
                        <Box display="block" component="div" className="loginForm">
                            <form onSubmit={formSubmit}>
                                <FormControl error={inputError.password.invalid}>
                                    <InputLabel shrink htmlFor="password">New Password</InputLabel>
                                    <Input
                                        id="password"
                                        type={passwordvisible?"text":"password"}
                                        placeholder="Password"
                                        InputLabelProps={{ shrink: true }}
                                        value={password}
                                        autoComplete="off"
                                        onChange={handleChangePassword}
                                        endAdornment={
                                            <InputAdornment position="end">
                                                <Tooltip title={passwordvisible ? "Hide Password" : "Show Password"} placement="top-end" arrow>
                                                    <IconButton aria-label="toggle password visibility" onClick={handlePasswordVisible} >
                                                        {passwordvisible ? <VisibilityIcon /> : <VisibilityOff />}
                                                    </IconButton>
                                                </Tooltip>
                                            </InputAdornment>
                                        }
                                    />
                                    <FormHelperText className={inputError.password.invalid?"":"d-none"} id="component-error-text">{inputError.password.message}</FormHelperText>
                                </FormControl>
                                <FormControl error={inputError.confirm_password.invalid}>
                                    <InputLabel shrink htmlFor="password">Confirm Password</InputLabel>
                                    <Input
                                        id="password"
                                        type={confirmpasswordvisible?"text":"password"}
                                        placeholder="Password"
                                        InputLabelProps={{ shrink: true }}
                                        value={confirm_password}
                                        autoComplete="off"
                                        onChange={handleChangeConfirmPassword}
                                        endAdornment={
                                            <InputAdornment position="end">
                                                <Tooltip title={confirmpasswordvisible ? "Hide Password" : "Show Password"} placement="top-end" arrow>
                                                    <IconButton aria-label="toggle password visibility" onClick={handleConfirmPasswordVisible} >
                                                        {confirmpasswordvisible ? <VisibilityIcon /> : <VisibilityOff />}
                                                    </IconButton>
                                                </Tooltip>
                                            </InputAdornment>
                                        }
                                    />
                                    <FormHelperText className={inputError.confirm_password.invalid?"":"d-none"} id="component-error-text">{inputError.confirm_password.message}</FormHelperText>
                                </FormControl>
                                <Box mt={4} mb={3}>
                                    <Button color="primary" type="submit" variant="contained" fullWidth disabled={loading}>Reset Password</Button>
                                </Box>
                            </form>
                        </Box>
                    </Grid>
                </Grid>
            </Container>
            <Message open={open} content={content} onClose={handleClose} />
        </div>
    )
}

export default ResetPassword;