import React, { useState, useEffect, Suspense } from 'react';
import { Switch, Route } from 'react-router-dom';
import './App.scss';
import Header from './component/Header';
import Backlog from './component/Backlog';
import Test from './component/Test';
import Login from './component/Auth/Login';
import ActivateAccount from './component/Auth/ActivateAccount';
import ForgotPassword from './component/Auth/ForgotPassword';
import ResetPassword from './component/Auth/ResetPassword';
import SignUp from './component/Auth/SignUp';
import Kanban from './component/Kanban';
import UserProfile from './component/UserProfile';
import PageNotFound from './component/Core/PageNotFound';
import DailyScrum from './component/DailyScrum';
import Loading from './component/Core/Loading';
import Onboarding from './component/Onboarding';
import Home from './component/Home'
import Setting from './component/Setting';


// const Onboarding = React.lazy(() => import('./component/Onboarding'));
// const Home = React.lazy(() => import('./component/Home'));
// function Loading() {
//   return <div className="centered-div">Loading...</div>;
// }

function App(props) {
  const [token, changeToken] = useState(sessionStorage.getItem('access_token'));
  const [tab, changeTab] = useState(0);
  const [step, changeStep] = useState(0);
  const [user, changeUserInfo] = useState(null);
  const [isLoggedIn, changeLoggedIn] = useState(false);
  const [dashTab, changeDashTab] = useState(0);
  const [onboarding, changeOnboarding] = useState(false);
  const handleTabChange = (value) => {
    changeTab(value);
  }

  const handleChangeLoggedIn = (value) => {
    if(value===false){
      changeToken(null);
      sessionStorage.removeItem("access_token");
      changeUserInfo(null);
      changeTab(0);
    }
    changeLoggedIn(value);
  }

  const handleUpdateUser = (value) => {
    changeUserInfo(value);
  }

  const handleDashTabChange = (value) => {
    changeDashTab(value);
  }

  const handleChangeToken = (value) => {
    if (value === "") {
      sessionStorage.removeItem("access_token");
      changeUserInfo(null);
      changeTab(0);
      changeLoggedIn(false);
      changeToken(null);
    }else{
      sessionStorage.setItem('access_token', value);
      changeToken(value);
      changeLoggedIn(true);
    }
  }

  const handleStep = (value) => {
    if (value === 3) { 
      changeOnboarding(false); 
    }else{
      changeOnboarding(true);
    }
    changeStep(value);
  }

  return (
    <React.Fragment>
      <Header onTabChange={handleTabChange} tab={tab} dashTab={dashTab} user={user} onDashTabChange={handleDashTabChange} onboarding={onboarding} loggedIn={isLoggedIn} updateToken={handleChangeToken} checkLoggedIn={handleChangeLoggedIn} updateUser={handleUpdateUser} />
        <Switch>
          <Route exact path="/"><Home token={token} loggedIn={isLoggedIn} checkLoggedIn={handleChangeLoggedIn} user={user} updateUser={handleUpdateUser} tab={tab} updateStep={handleStep} /></Route>
          <Route exact path="/test"><Test /></Route>
          <Route exact path="/login"><Login loggedIn={isLoggedIn} token={token} checkLoggedIn={handleChangeLoggedIn} updateToken={handleChangeToken} updateUser={handleUpdateUser} updateStep={handleStep} /></Route>
          <Route exact path="/signup"><SignUp loggedIn={isLoggedIn} token={token} checkLoggedIn={handleChangeLoggedIn} updateToken={handleChangeToken} updateUser={handleUpdateUser} /></Route>
          <Route exact path="/activate-account"><ActivateAccount loggedIn={isLoggedIn} /></Route>
          <Route exact path="/forgot-password"><ForgotPassword loggedIn={isLoggedIn} /></Route>
          <Route exact path="/reset-password"><ResetPassword loggedIn={isLoggedIn} /></Route>
          <Route exact path="/onboarding"><Onboarding loggedIn={isLoggedIn} token={token} step={step} onboarding={onboarding} user={user} updateStep={handleStep} checkLoggedIn={handleChangeLoggedIn} updateUser={handleUpdateUser} /></Route>
          <Route exact path="/backlog"><Backlog loggedIn={isLoggedIn} token={token} checkLoggedIn={handleChangeLoggedIn} updateUser={handleUpdateUser} onboarding={onboarding} /></Route>
          <Route exact path="/kanban"><Kanban loggedIn={isLoggedIn} token={token} checkLoggedIn={handleChangeLoggedIn} updateUser={handleUpdateUser} onboarding={onboarding} /></Route>
          <Route exact path="/user-profile"><UserProfile loggedIn={isLoggedIn} token={token} checkLoggedIn={handleChangeLoggedIn} updateUser={handleUpdateUser} onboarding={onboarding}/></Route>
          <Route exact path="/project/:project_id/scrum"><DailyScrum loggedIn={isLoggedIn} token={token} checkLoggedIn={handleChangeLoggedIn} updateUser={handleUpdateUser} onboarding={onboarding} /></Route>
          <Route exact path="/setting"><Setting loggedIn={isLoggedIn} token={token} checkLoggedIn={handleChangeLoggedIn} updateUser={handleUpdateUser} onboarding={onboarding} /></Route>
          <Route exact path="/loading"><Loading loggedIn={isLoggedIn} token={token} checkLoggedIn={handleChangeLoggedIn} updateUser={handleUpdateUser} onboarding={onboarding} /></Route>
          <Route path="*"><PageNotFound /></Route>
        </Switch>
      {/* <Suspense fallback={Loading}>
      </Suspense> */}
    </React.Fragment>
  );
}

export default App;