import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import './HeaderTab.scss';
const firstTabList = ["Why Scrumbooster?", "Pricing", "Help"];
const secondTabList = ["Dashboard", "Scrumboard", "Add-ons"];
function HeaderTab(props) {
    var tabs = [];
    switch(props.location){
        case '/':
            tabs = (props.onboarding?[]:(props.loggedIn?secondTabList:firstTabList));
            break;
        case '/onboarding':
            tabs = [];
            break;
        case '/login':
            tabs = [];
            break;
        case '/signup':
            tabs = [];
            break;
        case '/activate-account':
            tabs = [];
            break;
        case '/backlog':
            tabs = [];
            break;
        case '/kanban':
            tabs = [];
            break;
        default:
            tabs = [];
            break;
    }
    return (
        <Tabs indicatorColor="primary" textColor="primary" aria-label="Navigation Tabs" value={props.value} onChange={props.onChange} TabIndicatorProps={{style: {backgroundColor: "#2979FF", height: '4px', borderRadius: '5px 5px 0 0' } }}>
            {tabs.map((value,index) => {
                return <Tab key={index} label={value} />
            })}
        </Tabs>
    );
}

export default HeaderTab;