import { useState, useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Box from '@material-ui/core/Box';
import IconButton from '@material-ui/core/IconButton';
import Link from '@material-ui/core/Link';
import Divider from '@material-ui/core/Divider';
import './ActivateAccount.scss';
import { useHistory, useLocation } from 'react-router-dom';
import Message from '../../Core/Message';
import environment from '../../../config/environment';
import { Button } from '@material-ui/core';
const axios = require('axios');
function useQuery() {
    return new URLSearchParams(useLocation().search);
}
function ActivateAccount(props) {
    const history = useHistory();
    const [open, setOpen] = useState(false);
    const [option, setOption] = useState("");
    const [activateLoading, setActivateLoading] = useState(true);
    const [alreadyActivated, setAlreadyActivated] = useState(false);
    const [success, setSuccess] = useState(false);
    const [content, setContent] = useState({ type: "success", message: "" });
    const handleClose = () => {
        setOpen(false);
    }

    const continueLogin = () => {
        history.push('/login');
    }
    const continueForgotPassword = () => {
        history.push('/forgot-password');
    }

    const query = useQuery();
    const email = query.get('email');
    const token = query.get('token');
    const mailSent = query.get('mail_sent');

    const requestAnotherLink = () => {
        if (email) {
            setActivateLoading(true);
            axios.post(environment.url + '/user/send-activate-account-mail', { email: email })
                .then(response => {
                    if (response.status === 200) {
                        setTimeout(function () {
                            history.push("/activate-account?mail_sent=success")
                            setActivateLoading(false);
                        }, 1000);
                    }
                })
                .catch((error) => {
                    setContent({ type: "error", message: error.response.data.message });
                    setOpen(true);
                    setTimeout(function () {
                        setOpen(false);
                        setActivateLoading(false);
                    }, 1000);
                });
        }
    }
    useEffect(() => {
        if (mailSent) {
            setOption("email-sent");
            setActivateLoading(false);
        } else if (email && token) {
            setOption("activate-account");
        }
    })
    // useEffect(() => {
    //     if (props.loggedIn) {
    //         history.push("/");
    //     }
    // }, [props.loggedIn])


    useEffect(() => {
        if (email && token) {
            setActivateLoading(true);
            axios.post(environment.url + '/user/activate-account', { email: email, token: token })
                .then(response => {
                    if (response.status === 200) {
                        setTimeout(function () {
                            setActivateLoading(false);
                            setSuccess(true);
                        }, 1000);
                    }
                })
                .catch((error) => {
                    setTimeout(function () {
                        setSuccess(false);
                        setActivateLoading(false);
                        setAlreadyActivated((error.response.data.code == 498) ? true : false);
                    }, 1000);
                });
        }
    }, [email, token])

    if ((email && token) || option == "email-sent") {
        return (
            <div className="activate-container" >
                <Container fixed>
                    <Grid container maxWidth="sm" align="center" justify="center">
                        <Grid item xs={2}></Grid>
                        <Grid item xs={8} className="activateStepContainer">
                            <Box id="activateStep-1" mb={3.5} className={option === "email-sent" ? "" : "d-none"}>
                                <Box component="div" mb={3.5}>
                                    <img src="/images/mailsent.svg" alt="Email Sent" height="100px" width="100px"></img>
                                </Box>
                                <Box component="div" display="block">
                                    <Typography variant="h2">Please check your mail</Typography>
                                    <Typography variant="body1">An email with activation link has been sent to you for verification.</Typography>
                                </Box>
                            </Box>
                            <Box id="activateStep-2" mb={3.5} className={(activateLoading && (option === "activate-account" || option === "email-sent")) ? "" : "d-none"}>
                                <Box component="div" mb={3.5}>
                                    <img src="/images/hangon.svg" alt="Hang on !"></img>
                                </Box>
                                <Box component="div" display="block">
                                    <Typography variant="h2">Hang on !</Typography>
                                    <Typography variant="body1">Thank you ! We are setting up an account for you.</Typography>
                                </Box>
                            </Box>
                            <Box id="activateStep-3" mb={3.5} className={(!activateLoading && success && option === "activate-account") ? "" : "d-none"}>
                                <Box component="div" mb={2.5}>
                                    <img src="/images/success.svg" alt="Completed" height="100px" width="100px"></img>
                                </Box>
                                <Box component="div" display="block">
                                    <Typography variant="h2">Congratulations!</Typography>
                                    <Typography variant="body1" > Your account has been sucessfully activated. Click continue to log in and create new workspace</Typography>
                                    <Box mt={4} mb={3}>
                                        <Button color="primary" type="submit" variant="contained" fullWidth onClick={continueLogin}>Continue</Button>
                                    </Box>
                                </Box>
                            </Box>
                            <Box id="activateStep-4" mb={3.5} className={(!alreadyActivated && !activateLoading && !success && option === "activate-account") ? "" : "d-none"}>
                                <Box component="div" mb={3.5}>
                                    <img src="/images/activation.svg" alt="Activation link expired"></img>
                                </Box>
                                <Box component="div" display="block">
                                    <Typography variant="h2">Activation link expired</Typography>
                                    <Typography variant="body1">Sorry! The activation link has been expired as you haven’t used it within 24 hours. </Typography>
                                    <Box mt={4} mb={3}>
                                        <Button color="primary" type="submit" variant="contained" fullWidth onClick={requestAnotherLink}>Request another link</Button>
                                    </Box>
                                </Box>
                            </Box>
                            <Box id="activateStep-5" mb={3.5} className={(alreadyActivated && option === "activate-account") ? "" : "d-none"}>
                                <Box component="div" mb={3.5}>
                                    <img src="/images/warning.svg" alt="Already have an account"></img>
                                </Box>
                                <Box component="div" display="block">
                                    <Typography variant="h2">Already have an account</Typography>
                                    <Typography variant="body1">Someone has already set up scrumbooster for your organisation. Ask your admin to get you started or set up using different Email address</Typography>
                                    <Box mt={4} mb={2}>
                                        <Button color="primary" type="submit" variant="contained" fullWidth onClick={continueLogin}>Log in</Button>
                                    </Box>
                                    <Link component="button" onClick={continueForgotPassword}>Forgot Password?</Link>
                                </Box>
                            </Box>
                        </Grid>
                        <Grid item xs={2}></Grid>
                    </Grid>
                </Container>
                <Message open={open} content={content} onClose={handleClose} />
            </div>
        )
    } else {
        return <></>
    }
}

export default ActivateAccount;