import {useEffect, useState} from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import IconButton from '@material-ui/core/IconButton';
import Box from '@material-ui/core/Box';
import { withStyles } from '@material-ui/core/styles';
import InputBase from '@material-ui/core/InputBase';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import InputAdornment from '@material-ui/core/InputAdornment';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import VisibilityIcon from '@material-ui/icons/Visibility';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Icon from '@material-ui/core/Icon';
import Tooltip from '@material-ui/core/Tooltip';

import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';

import './ForgotPassword.scss';
import Message from '../../Core/Message';
import FormHelperText from '@material-ui/core/FormHelperText';
import Input from '@material-ui/core/Input';
import environment from '../../../config/environment';
import validation from '../../../config/validation';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const BootstrapInput = withStyles((theme) => ({}))(InputBase);
const icons = [{ icon: 'Microsoft', path: '/images/icon-micro.svg', height: "19px", width: "20px" }, { icon: 'Google', path: '/images/icon-google.svg', height: "20px", width: "20px" }, { icon: 'GitHub', path: '/images/icon-github.svg', height: "20px", width: "20px" }];
const axios = require('axios');

function ForgotPassword(props) {
    const history = useHistory();
    const [open, setOpen] = useState(false);
    const [email, changeEmail] = useState("");
    const [content, setContent] = useState({ type: "success", message: "" });
    const [inputError, changeInputError] = useState({ email: { invalid: false, message: "" } })
    const handleClose = () => {
        setOpen(false);
    }

    const handleEmail = (event) => {
        changeEmail(event.target.value);
        var error = { ...inputError };
        error.email = { invalid: false, message: "" };
        if (event.target.value.trim() !== "") { changeInputError(error) }
    }

    const submitForm = (event) =>{
        event.preventDefault();
        var validateResponse = validation({
            email: email
        });

        if (validateResponse.invalid) {
            var errors = validateResponse.errors;
            changeInputError({
                email: { invalid: errors.email.invalid, message: errors.email.message }
            })
        }

        if (email.trim() !== "" && !validateResponse.invalid) {
            axios.post(environment.url + '/user/forgot-password', {email: email})
            .then(response => {
                if (response.status === 200) {
                    // props.updateToken(response.data.data.access_token);
                    // props.updateUser(response.data.data.user);
                    // props.updateStep(response.data.data.user.steps);
                    // // sessionStorage.setItem('access_token', response.data.data.access_token);
                    // // setTimeout(function () {
                    // //     history.push(response.data.data.user.steps > 2 ? '/' : '/onboarding');
                    // // }, 1000);
                    setOpen(true);
                    setContent({ type: "success", message: response.data.message });
                    changeEmail("");
                    // if (response.data.data.user.steps > 2) {
                    //     history.push("/");
                    // } else {
                    //     history.push("/onboarding");
                    // }
                }
            })
            .catch((error) => {
                setContent({ type: "error", message: error.response.data.message });
                setOpen(true);
            });
        }
    }
    useEffect(() => {
        if(props.loggedIn){
            history.push("/");
        }
    }, [props.loggedIn])
    return (
        <div className="form-container">
            <Container fixed >
                <Grid container spacing={3} maxWidth="sm">
                    <Grid item xs="12" align="center">
                        <Typography variant="h2">Forgot Password</Typography>
                        <Box className="form-desc"><Typography variant="body1">Please enter verified Email ID. We’ll send a link to reset your password.</Typography></Box>

                        <Box display="block" component="div" className="loginForm">
                            <Box className="alerts simple-alerts d-none" mb={5}>
                                <Alert variant="filled" severity="success" color="success">An email has been sent with password reset link. Please follow the link in Email to complete your request.</Alert>
                            </Box>
                            <form onSubmit={submitForm}>
                                <FormControl error={inputError.email.invalid}>
                                    <InputLabel shrink htmlFor="email">Email ID</InputLabel>
                                    <Input
                                        id="email"
                                        type="email"
                                        value={email}
                                        onChange={handleEmail}
                                        placeholder="Email"
                                    />
                                    <FormHelperText className={inputError.email.invalid ? "" : "d-none"} id="component-error-text">{inputError.email.message}</FormHelperText>
                                </FormControl>
                                <Box mt={4} mb={3}>
                                    <Button color="primary" type="submit" variant="contained" fullWidth>Next</Button>
                                </Box>
                            </form>
                            {/* <Box mt={4} mb={3} mx='auto' className="authorizeWith" display="flex" alignItems="center" justifyContent="center" flexDirection="column">
                                <Box display="flex" alignItems="center" justifyContent="center">
                                    <Typography variant="overline" className="authorizeText">OR AUTHORIZE WITH</Typography>
                                </Box>
                                <Box display="flex" alignItems="center" justifyContent="space-between" className="authorizeBtn" mt={2} mb={3}>
                                    {icons.map(value => {
                                        return (<Button variant="outlined" startIcon={<Icon><img alt={value.icon} src={value.path} height={value.height} width={value.width} /></Icon>}>{value.icon}</Button>);
                                    })}
                                </Box>
                            </Box> */}
                        </Box>
                    </Grid>
                </Grid>
            </Container>
            <Message open={open} content={content} onClose={handleClose} />
        </div>
    )
}

export default ForgotPassword;