import React, { useState, useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Steps from '../Steps';
import TextField from '@material-ui/core/TextField';
import ButtonGroup from "@material-ui/core/ButtonGroup";
import Box from '@material-ui/core/Box';
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Grow from "@material-ui/core/Grow";
import Paper from "@material-ui/core/Paper";
import Popper from "@material-ui/core/Popper";
import MenuItem from "@material-ui/core/MenuItem";
import MenuList from "@material-ui/core/MenuList";
import { useHistory, useLocation } from 'react-router-dom';
import OAuth2 from '../Auth/OAuth2';
import environment from '../../config/environment';
import Message from '../Core/Message';
import './Onboarding.scss';

const axios = require('axios');
const options = ["Github", "Gitlab (coming soon)", "Jira (coming soon)"];
const imagesrcs = ["/images/github.webp", "/images/gitlab.svg", "/images/jira.svg"];

function SplitButton(props) {
    const [open, setOpen] = React.useState(false);
    const [checkAuth, setCheckAuth] = React.useState(false);
    const anchorRef = React.useRef(null);
    const [selectedIndex, setSelectedIndex] = React.useState(0);
    const handleClick = () => {
        setCheckAuth(true);
    };
    const handleMenuItemClick = (event, index) => {
        setSelectedIndex(index);
        setOpen(false);
    };
    const handleToggle = () => {
        setOpen((prevOpen) => !prevOpen);
    };
    const handleClose = (event) => {
        if (anchorRef.current && anchorRef.current.contains(event.target)) {
            return;
        }
        setOpen(false);
    };

    return (
        <Grid container direction="column" alignItems="center">
            <Grid item xs={12}>
                <ButtonGroup
                    variant="contained"
                    ref={anchorRef}
                    aria-label="split button"
                >
                    <Button
                        size="small"
                        aria-controls={open ? "split-button-menu" : undefined}
                        aria-expanded={open ? "true" : undefined}
                        aria-label="select merge strategy"
                        aria-haspopup="menu"
                        onClick={handleToggle}
                        style={{ background: "#d7e4fb" }}
                    >
                        <img src={imagesrcs[selectedIndex]} style={{ width: '30px' }}></img>{" "}
                        <svg
                            className="MuiSvgIcon-root"
                            focusable="false"
                            viewBox="0 0 24 24"
                            aria-hidden="true"
                        >
                            <path d="M7 10l5 5 5-5z"></path>
                        </svg>
                    </Button>
                    <OAuth2 check={checkAuth} />
                    <Button color="primary" onClick={handleClick}>
                        Connect to {options[selectedIndex]}
                    </Button>
                </ButtonGroup>
                <Popper
                    open={open}
                    anchorEl={anchorRef.current}
                    role={undefined}
                    transition
                    disablePortal
                    style={{ zIndex: 9 }}
                >
                    {({ TransitionProps, placement }) => (
                        <Grow
                            {...TransitionProps}
                            style={{
                                transformOrigin:
                                    placement === "bottom" ? "left top" : "left bottom",
                            }}
                        >
                            <Paper className="split-menu-git">
                                <ClickAwayListener onClickAway={handleClose}>
                                    <MenuList id="split-button-menu">
                                        {options.map((option, index) => (
                                            <MenuItem

                                                key={option}
                                                disabled={index !== 0}
                                                selected={index === selectedIndex}
                                                onClick={(event) => handleMenuItemClick(event, index)}
                                                style={{ paddingLeft: '30px' }}
                                            >
                                                {index === selectedIndex && (
                                                    <i style={{ position: "absolute", left: '7px', top: '7px' }}>
                                                        <svg stye={{ width: '17px' }} className="MuiSvgIcon-root jss97" focusable="false" viewBox="0 0 24 24" aria-hidden="true"><path d="M9 16.17L4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41z" fill="#4f91ff"></path></svg>
                                                    </i>
                                                )}
                                                <img src={imagesrcs[index]} style={{ width: '25px', padding: '0 5px' }}></img>
                                                {option}
                                            </MenuItem>
                                        ))}
                                    </MenuList>
                                </ClickAwayListener>
                            </Paper>
                        </Grow>
                    )}
                </Popper>
            </Grid>
        </Grid>
    );
}

function useQuery() {
    return new URLSearchParams(useLocation().search);
}

function Onboarding(props) {
    const [imgSrc, changeImgSrc] = React.useState("/images/activate-acc.png");
    const history = useHistory();
    const [workspace, changeWorkspace] = useState("");
    const [file, changeFile] = useState(null);
    const [open, setOpen] = useState(false);
    const [content, setContent] = useState({ type: "success", message: "" });
    const handleClose = () => {
        setOpen(false);
        // setContent({ type: "success", message: "" });
    }
    const query = useQuery();
    const code = query.get('code');
    const state = query.get('state');

    const createWorkspace = () => {
        props.updateStep(1);
    }
    const githubConnect = () => {
        props.updateStep(2);
    }

    const githubConnectLater = () => {
        if (props.token) {
            axios.post(environment.url + '/github/connect-later', { token: props.token })
                .then(response => {
                    if (response.status === 200) {
                        console.log('github/connect-later', response);
                        props.updateStep(response.data.data.user.steps);
                        history.push(response.data.data.user.steps > 2 ? '/' : '/onboarding');
                        // setTimeout(function(){ history.push(response.data.data.user.steps>2?'/':'/onboarding'); }, 1500);
                    }
                })
                .catch((error) => {
                    setContent({ type: "error", message: error.response.data.message });
                    setOpen(true);
                });
        }
    }

    const handleSprintPlanning = () => {
        if (props.token) {
            axios.post(environment.url + '/user/manage-sprint-planning', { token: props.token })
                .then(response => {
                    console.log('outside')
                    if (response.status === 200) {
                        console.log('inside', response);
                        props.checkLoggedIn(true);
                        props.updateUser(response.data.data.user);
                        setOpen(true);
                        setContent({ type: "success", message: response.data.message });
                        props.updateStep(response.data.data.user.steps);
                        history.push('/');
                    }
                })
                .catch((error) => {
                    // history.push('/login');
                });
        }
    }

    const handleImageUpload = (event) => {
        console.log('file=>', event.target.files)
        var file = event.target.files[0];
        if (file) {
            changeFile(file);
            var reader = new FileReader();
            var url = reader.readAsDataURL(file);
            reader.onloadend = function (e) {
                changeImgSrc(reader.result)
            }.bind(this);
        }

    }

    const handleWorkspace = (event) => {
        changeWorkspace(event.target.value);
    }

    const handleFormSubmit = (event) => {
        event.preventDefault();
        if (file && workspace.trim() !== "" && props.token) {
            var formData = new FormData();
            formData.append("name", workspace.replace("'", "`"));
            formData.append("avatar", file);
            formData.append("token", props.token);
            axios.post(environment.v2url + '/workspace/create', formData)
                .then(response => {
                    if (response.status === 201) {
                        props.updateStep(props.step + 1);
                    }
                })
                .catch((error) => {
                    setContent({ type: "error", message: error.response.data.message });
                    setOpen(true);
                    setTimeout(function () { setOpen(false); }, 2000);
                });
        }
        if (!file) {
            setContent({ type: "error", message: "Image is required" });
            setOpen(true);
            setTimeout(function () { setOpen(false); }, 2000);
        }
        if (workspace.trim() === "") {
            setContent({ type: "error", message: "Workspace name cannot be empty" });
            setOpen(true);
            setTimeout(function () { setOpen(false); }, 2000);
        }
    }

    useEffect(async () => {
        if (props.token) {
            axios.post(environment.url + '/user/info', { token: props.token })
                .then(response => {
                    if (response.status === 200) {
                        props.checkLoggedIn(true);
                        props.updateUser(response.data.data.user);
                        if (response.data.data.user.steps <= 2) {
                            props.updateStep(response.data.data.user.steps)
                        } else {
                            history.push("/");
                        }
                    } else {
                        props.checkLoggedIn(false);
                        history.push('/login');
                    }
                })
                .catch((error) => {
                    setContent({ type: "error", message: error.response.data.message });
                    setOpen(true);
                    setTimeout(function () { setOpen(false); }, 1500);
                });
        } else {
            history.push('/login')
        }
    }, [props.token]);

    useEffect(() => {
        if (code && props.step === 1) {
            var object = { token: props.token, code: code };
            if (state) { object.state = state; }
            axios.post(environment.url + '/github/save-access-token', object)
                .then(response => {
                    if (response.status === 200) {
                        props.checkLoggedIn(true);
                        props.updateUser(response.data.data.user);
                        setOpen(true);
                        setContent({ type: "success", message: response.data.message });
                        if (response.data.data.user.steps <= 2) {
                            props.updateStep(response.data.data.user.steps)
                        } else {
                            // history.push("/");
                        }
                        history.push('/onboarding');
                    }
                })
                .catch((error) => {
                    setContent({ type: "error", message: error.response.data.message });
                    setOpen(true);
                    setTimeout(function () { setOpen(false); }, 1500);
                });
        }
    }, [code, props.step])

    useEffect(() => {
        if (props.step > 0 && props.step < 3 && props.user && props.user.workspace_id) {
            axios.get(environment.url + '/workspace/' + props.user.workspace_id)
                .then(response => {
                    if (response.status === 200) {
                        changeImgSrc(response.data.data.workspace.avatar);
                        changeWorkspace(response.data.data.workspace.name)
                    }
                })
                .catch((error) => {
                    setContent({ type: "error", message: error.response.data.message });
                    setOpen(true);
                    setTimeout(function () { setOpen(false); }, 1500);
                });
        }
    }, [props.step, props.user, code]);


    return (
        <React.Fragment>
            {/* <Secured/> */}
            <div className="onboard-container">
                <Steps currentStep={props.step} />
                <Container fixed className={props.step == 0 ? "org-board" : "d-none"} >
                    <Grid container spacing={3} maxWidth="sm">
                        <Grid item xs="12" align="center">
                            <Typography variant="h2" gutterBottom>Glad to have you onboard!</Typography>
                            <Typography variant="h4">Get started by creating a workspace to collaborate<br></br>with other people in your organization.</Typography>
                        </Grid>
                        <Grid item xs="4" align="center"></Grid>
                        <Grid item xs="4" align="center" className="org-git">
                            {/* <input accept="image/*" id="upload-logo-circle" type="file" onChange={handleImageUpload} hidden />
                            <label htmlFor="upload-logo-circle">
                                <IconButton aria-label="Upload" className="logo-upload" component="span">
                                    <img src={imgSrc} alt="Upload Icon"></img>
                                </IconButton>
                            </label> */}
                            <Box alignItems='center' display='flex' justifyContent='center' flexDirection='column'>
                                <Box>
                                    <input accept="image/*" id="upload-company-logo" type='file' onChange={handleImageUpload} hidden />
                                    <label htmlFor="upload-company-logo">
                                        <Button component="span" className="upload-logo" >
                                            <Paper elevation={0}>
                                                <div className="org-logo">
                                                    <img src={imgSrc} alt="Upload Icon" className={imgSrc === "/images/activate-acc.png" ? "" : "imgShadow"}></img>
                                                </div>
                                                <img src="/images/camera.svg" className="camera-btn" alt="Scrumbooster"></img>
                                            </Paper>
                                        </Button>
                                    </label>
                                </Box>
                            </Box>
                            <Box className="btnText" component="div" mt={0.5} mb={1}>Upload Logo</Box>

                            <div align="left" className="org-form">
                                <form noValidate autoComplete="off" onSubmit={handleFormSubmit}>
                                    <TextField
                                        id="standard-password-input"
                                        label="Workspace Name"
                                        placeholder="Organization Name"
                                        value={workspace}
                                        onChange={handleWorkspace}
                                        fullWidth
                                        margin="normal"
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    />
                                    <Button type="submit" color="primary" variant="contained" className="workspace-btn" fullWidth>Create Workspace</Button>
                                </form>
                            </div>
                        </Grid>
                        <Grid item xs="4" align="center"></Grid>

                    </Grid>
                </Container>
                <Container fixed className={props.step == 1 ? "org-git" : "d-none"}>
                    <Grid container spacing={3} maxWidth="sm">
                        <Grid item xs="12" align="center">
                            <Typography variant="h2" gutterBottom>Yay! You have created a new workspace<br></br>for your organization.</Typography>
                            <Box alignItems='center' display='flex' justifyContent='center' flexDirection='column'>
                                <Box>
                                    <input accept="image/*" id="upload-company-logo" type='file' hidden />
                                    <label htmlFor="upload-company-logo">
                                        <Button component="span" className="upload-logo" >
                                            <Paper elevation={0}>
                                                <div className="org-logo">
                                                    <img src={imgSrc} alt="Company Logo" className={imgSrc === "/images/activate-acc.png" ? "" : "imgShadow"}></img>
                                                </div>
                                                <img src="/images/camera.svg" className="camera-btn" alt="Scrumbooster"></img>
                                            </Paper>
                                        </Button>
                                    </label>
                                </Box>
                                <Box pt={1}>
                                    <Typography variant="body2" gutterBottom>Organization name: <span className="org-name">{workspace}</span> <a href="#">Edit</a></Typography>
                                </Box>
                            </Box>
                        </Grid>
                        <Grid item xs="4" align="center"></Grid>
                        <Grid item xs="4" align="center">
                            <div>
                                <form noValidate autoComplete="off">
                                    <Typography variant="body1" className="git-setup-text" gutterBottom>Now you can go for Github Setup</Typography>
                                    <Grid container direction="column" alignItems="center">
                                        {/* Dropdown Holder  */}
                                        <SplitButton connectGithub={githubConnect}></SplitButton>
                                    </Grid>
                                    <Button variant="outlined" color="primary" className="later-btn" onClick={githubConnectLater}>I'll do it later </Button>
                                </form>
                            </div>
                        </Grid>
                        <Grid item xs="4" align="center"></Grid>
                    </Grid>
                </Container>
                <Container fixed className={props.step == 2 ? "org-success" : "d-none"}>
                    <Grid item xs="12" align="center">
                        <Box alignItems='center' display='flex' justifyContent='center' flexDirection='column'>
                            <Box>
                                <Paper elevation={3} className="git-logo">
                                    <img src="/images/blk-github.svg" alt="" />
                                    <span className="tick icon-tick"></span>
                                </Paper>
                            </Box>
                        </Box>
                        <Typography variant="h2">
                            You have successfully connected to GitHub
                        </Typography>
                        <Typography variant="body1" gutterBottom>
                            You are able to see your GitHub repositories and data on
                            Scrumbooster.<br></br>Continue to set up your teams.
                        </Typography>
                        <Button variant="contained" color="primary" onClick={handleSprintPlanning}>
                            Manage Sprint Planning and Scrum Meetings
                        </Button>
                    </Grid>
                </Container>
            </div>
            <Message open={open} content={content} onClose={handleClose} />
        </React.Fragment>
    );
}
export default Onboarding;