import React, { useState, useEffect } from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import InputBase from '@material-ui/core/InputBase';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Radio from '@material-ui/core/Radio';
import Checkbox from '@material-ui/core/Checkbox';
import Switch from '@material-ui/core/Switch';
import Avatar from '@material-ui/core/Avatar';
import './Dialogbox.scss';
import environment from '../../config/environment';
import Moment from 'react-moment';
import 'moment-timezone';

const axios = require('axios');

function Dialogbox(props) {
    const [selectedRepo, changeRepoOption] = useState("");
    const [searchRepo, changeSearchRepo ] = useState("");
    const [searchMember, changeSearchMember ] = useState("");
    const [filteredRepoList, changeFilteredRepo] = useState([]);
    const [teamMembers, changeTeamMembers] = useState([]);
    const [filteredMemberList, changeFilteredMember] = useState([]);
    const [inviteAll, changeInviteAll] = useState(false);
    const [inviteSelected, changeInviteSelected] = useState(false);
    const handleRepoChange = (event) => {
        var repos = [...props.repos];
        repos.forEach(value=>{
            if(value.name === event.target.value){
                changeRepoOption(value);        
            }
        })
    };

    const handleInviteAll = () =>{
        changeInviteSelected(!inviteAll);
        var tempTeamMembers = [...teamMembers];
        tempTeamMembers.forEach(value=>{
            if(value.email!==""){
                value.checked = !inviteAll;
            }
        })
        changeInviteAll(!inviteAll);
    }

    const handleSwitchChange = (index) => {
        var tempTeamMembers = [...teamMembers];
        tempTeamMembers[index]['checked'] = !(tempTeamMembers[index]['checked'])
        changeTeamMembers(tempTeamMembers);
    };

    const getTeamMembers = (params) =>{
        axios.post(environment.url + '/github/members-list', params)
        .then(response => {
            if (response.status === 200) {
                var members = [...response.data.data.members];
                members.forEach(value=>{
                    value.checked = false;
                })
                changeTeamMembers(members);
                // getTeamMembers({token: props.token, repository: selectedRepo.name});
                props.updateCurrentStep(1);
            }
        })
        .catch((error) => {
            // setContent({ type: "error", message: error.response.data.message });
            // setMessageOpen(true);
            // setTimeout(function () { setMessageOpen(false); }, 2000);
        });
    }

    const handleAddTeamMember = () => {
        if(props.token && selectedRepo.name!=="" && (props.project?.repository?.name!==selectedRepo.name)){
            axios.post(environment.url + '/project/add-update-repository', {token: props.token, project_id: props.project.project_id, name: selectedRepo.name, github_id: selectedRepo.id})
                .then(response => {
                    if (response.status === 200) {
                        getTeamMembers({token: props.token, repository: selectedRepo.name});
                        // props.updateCurrentStep(1);
                        props.updateRepo({project_id: props.project.project_id, data: response.data.data[0]})
                    }
                })
                .catch((error) => {
                    // setContent({ type: "error", message: error.response.data.message });
                    // setMessageOpen(true);
                    // setTimeout(function () { setMessageOpen(false); }, 2000);
                });
        }else{
            if(selectedRepo){
                getTeamMembers({token: props.token, repository: selectedRepo.name});
                // props.updateCurrentStep(1);
            }
        }
    }

    const handleClose = () =>{
        changeRepoOption("");
        props.onClose();
    }

    const handleSearchChange = (event) =>{
        changeSearchRepo(event.target.value);
        if(event.target.value.trim()!==""){
            var tempRepoList = filteredRepoList.filter(function(repo){
                if(repo.name === selectedRepo.name){return true;}
                repo = repo.name.toLowerCase();
                return repo.indexOf(event.target.value.toLowerCase()) > -1; 
            });
            changeFilteredRepo(tempRepoList);
        }else{
            changeFilteredRepo(props.repos);
        }
    }

    const handleSearchMember = (event) =>{
        changeSearchMember(event.target.value);
        if(event.target.value.trim()!==""){
            var tempMemberList = teamMembers.filter(function(member){
                return ((member?.name?.toLowerCase().indexOf(event.target.value.toLowerCase()) > -1) || (member.login.toLowerCase().indexOf(event.target.value.toLowerCase()) > -1) || (member.email.toLowerCase().indexOf(event.target.value.toLowerCase()) > -1)); 
            });
            changeFilteredMember(tempMemberList);
        }else{
            changeFilteredMember(teamMembers);
        }
    }

    const handleSendInvitation = () =>{
        var emails = [];
        var usernames = [];
        var avatars = [];
        teamMembers.forEach(value=>{
            if(value.checked && value.email !== ""){
                emails.push(value.email);
                usernames.push(value.name?value.name:value.login);
                avatars.push(value.avatarUrl);
            }
        })
        // console.log('emails=>',emails, 'usernames=>',usernames, ', avatars=>',avatars);
        if(props.token && props.project.project_id){
            axios.post(environment.url + '/project/add-members', {token: props.token, project_id: props.project.project_id, data: {emails: emails.join(), usernames: usernames.join(), avatars: avatars.join()}})
            .then(response => {
                if (response.status === 200) {
                    props.updateMember({project_id: props.project.project_id,members: response.data.data.members});
                    handleClose();
                    props.showSuccess(response.data.message);
                }
            })
            .catch((error) => {
                // setContent({ type: "error", message: error.response.data.message });
                // setMessageOpen(true);
                // setTimeout(function () { setMessageOpen(false); }, 2000);
            });
        }
    }

    useEffect(() => {
        if(props.project?.repository?.name){
            var repos = [...props.repos];
            repos.forEach(value=>{
                if(value.name === props.project.repository.name){
                    changeRepoOption(value);        
                }
            })
        }
    }, [props.project])

    useEffect(() => {
        changeFilteredRepo(props.repos);
    }, [props.repos])

    useEffect(() => {
        var conditionChecked = false;
        var allChecked = true;
        teamMembers.forEach(value=>{
            if(!conditionChecked && value.checked){
                console.log('value1=>',value);
                conditionChecked = true;
                changeInviteSelected(true);
            }
            if(value.email!=="" && !value.checked){
                console.log('allChecked false');
                allChecked = false;
                // changeInviteAll(false);
            }
        })
        changeInviteAll(allChecked);

        if(!conditionChecked){
            changeInviteSelected(false);
        }
        changeFilteredMember(teamMembers);
    }, [teamMembers])

    return (
        <Dialog
            open={props.open}
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            maxWidth={"lg"}
            fullWidth={true}
            className="addDialog"
        >
            {/* For Dialog Title Format Starts */}
            <DialogTitle id="alert-dialog-title" >
                <Box display="flex" width="100%" className="dialog-title">
                    <Box display="flex" alignItems="center">
                        <Box pr={1} display="flex"><img src="/images/github.webp" width="18" height="18" alt="Github Logo " /></Box>
                        <Box display="block"><Typography component="span" variant="h6"> Add Repository</Typography></Box>
                    </Box>
                    <Box>
                        <IconButton aria-label="delete" onClick={handleClose}>
                            <CloseIcon fontSize="" />
                        </IconButton>
                    </Box>
                </Box>
            </DialogTitle>
            {/* For Dialog Title Format Ends */}

            {/* For Dialog Roadmap Format Starts */}
            <Box display="flex" className="dialogStepper" pb={3}>
                <Stepper activeStep={props.currentStep} className="stepperSM">
                    <Step>
                        <StepLabel><Typography variant="caption" className="bold500" display="block">Add Repository</Typography></StepLabel>
                    </Step>
                    <Step>
                        <StepLabel><Typography variant="caption" className="bold500" display="block">Add Team</Typography></StepLabel>
                    </Step>
                </Stepper>
            </Box>
            {/* For Dialog Roadmap Format Ends */}

            {/* For Dialog Content Format Starts */}
            <DialogContent>
                {/* For Step 1 Component Format Starts  */}
                <Box className="repo-search">
                    <Box display="flex" justifyContent="space-between" alignItems="center" mb={1} className={props.currentStep === 0 ? "" : "d-none"}>
                        <Box className="repo-search__label"><Typography variant="h6">Select Repository</Typography></Box>
                        <Box className="repo-search__btns">
                            <Button variant="outlined" color="primary" onClick={handleClose} size="medium">Cancel</Button>
                            <Button variant="contained" color="primary" size="medium" onClick={handleAddTeamMember} disabled={!selectedRepo}>Add Team Members</Button>
                        </Box>
                    </Box>
                    <Box display="flex" className={props.currentStep === 0 ? "" : "d-none"}>
                        <Box className="repo-search__panel" mb={3}>
                            <InputBase placeholder="Find Repository" inputProps={{ 'aria-label': 'github repo search' }} value={searchRepo} onChange={handleSearchChange} />
                            <IconButton type="button" aria-label="search" className="icon-search"> </IconButton>
                        </Box>
                    </Box>
                </Box>

                <TableContainer className={props.currentStep === 0 ? "" : "d-none"}>
                    <Table className="repo-table" aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell >Repository</TableCell>
                                <TableCell align="left">Updated</TableCell>
                                <TableCell align="center">Action</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody className={filteredRepoList.length===0?"d-none":""}>
                            {filteredRepoList.map((value,index)=>{
                                return <TableRow key={index}>
                                    <TableCell align="left">{value.name}</TableCell>
                                    <TableCell align="left"><Moment fromNow>{value.updatedAt}</Moment></TableCell>
                                    <TableCell align="center"><Radio checked={selectedRepo.name === value.name} onChange={handleRepoChange} value={value.name} name="radioRepo" inputProps={{ 'aria-label': 'Option-1' }} color="primary" /></TableCell>
                                </TableRow>
                            })}
                        </TableBody>
                        {/* Not found table */}
                        <TableBody className={filteredRepoList.length===0?"":"d-none"}>
                            <TableCell align="center" colSpan={3}><Box display="flex" justifyContent="center" alignItems="center" className="MuiTypography-h3" style={{color: '#787885'}}>Search results not found</Box></TableCell>
                        </TableBody>
                    </Table>
                </TableContainer>
                {/* For Step 1 Component Format Ends  */}

                {/* For Step 2 Component Format Starts  */}
                <Box className={props.currentStep === 1 ? "" : "d-none"}>
                    <Box className="addTeam" display="flex" alignItems="center" justifyContent="space-between" pb={1} mb={2} >
                        <Box pl={1}><Typography variant="h6">Add Team</Typography></Box>
                        <Box className="repo-search__btns">
                            <Button variant="outlined" color="primary" onClick={handleClose}>Cancel</Button>
                            <Button variant="outlined" color="primary" onClick={handleClose}>Skip</Button>
                            <Button variant="contained" color="primary" disabled={!inviteSelected} onClick={handleSendInvitation}>Done</Button>
                        </Box>
                    </Box>
                </Box>
                <Paper elevation={2} className={props.currentStep === 1 ? "" : "d-none"}>
                    <Box className="team">
                        <Box display="flex" justifyContent="space-between" p={2} pb={0}>
                            <Box className="team__info">
                                <Box component="div" className="MuiTypography-caption">Selected Repository</Box>
                                <Box component="div"><Typography variant="subtitle2">{selectedRepo?.name}</Typography></Box>
                            </Box>
                            <Box display="flex" className="team__search">
                                <InputBase placeholder="Find Members" value={searchMember} onChange={handleSearchMember} inputProps={{ 'aria-label': 'github repo search' }} />
                                <IconButton type="button" aria-label="search" className="icon-search"> </IconButton>
                            </Box>
                        </Box>
                        <TableContainer pt={0}>
                            <Table className="team-member-table" aria-label="Team Member Table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell>Name</TableCell>
                                        <TableCell>User Name</TableCell>
                                        <TableCell>Email ID</TableCell>
                                        <TableCell align="right"><Checkbox color="primary" onChange={handleInviteAll} value={inviteAll} checked={inviteAll} />Invite All</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {filteredMemberList.map((value,index)=>{
                                        return <TableRow key={index}>
                                            <TableCell align="left">
                                                <Box display="flex" component="div" >
                                                    <Avatar className="teamAvatar" src={value.avatarUrl}></Avatar>
                                                    <Box pl={2}><Typography variant="body1">{value.name}</Typography></Box>
                                                </Box>
                                            </TableCell>
                                            <TableCell><Typography variant="body1">{value.login}</Typography></TableCell>
                                            <TableCell><Typography variant="body1">{value.email}</Typography></TableCell>
                                            <TableCell align="right"><Switch disabled={value.email===""} checked={value.checked} onChange={()=>handleSwitchChange(index)} name="member1" inputProps={{ 'aria-label': 'secondary checkbox' }} color="primary" /></TableCell>
                                        </TableRow>
                                    })}
                                </TableBody>
                                {/* Not found table */}
                                <TableBody className={filteredMemberList.length===0?"":"d-none"}>
                                    <TableCell align="center" colSpan={4}><Box display="flex" justifyContent="center" alignItems="center" className="MuiTypography-h3" style={{color: '#787885'}}>Search results not found</Box></TableCell>
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Box>
                </Paper>
                {/* For Step 2 Component Format Ends  */}

            </DialogContent>
            {/* For Dialog Content Fromat Ends */}

        </Dialog>
    )
}
export default Dialogbox;