import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import CloseIcon from '@material-ui/icons/Close';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import Chip from '@material-ui/core/Chip';
import TextField from '@material-ui/core/TextField';
import Link from '@material-ui/core/Link';
import FileCopyOutlinedIcon from '@material-ui/icons/FileCopyOutlined';

import InputBase from '@material-ui/core/InputBase';
import './Share.scss';

function Share(props) {
    const handleClose = () => {
        props.onClose();
    };
    const handleDelete = () => {
        console.info('You clicked the delete icon.');
    };
    return (
        <Dialog
            open={props.open}
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            overlayStyle={{backgroundColor: 'red'}}>
            <DialogTitle id="alert-dialog-title">
                <Box display="flex" alignItems="center" justifyContent="space-between">
                    <Typography variant="h6">Share Project</Typography>
                    <Box>
                        <IconButton aria-label="delete" onClick={props.onClose}>
                            <CloseIcon />
                        </IconButton>
                    </Box>
                </Box>
            </DialogTitle>
            <DialogContent>
                <Box>
                    <Box component="div" mb={1}><Typography variant="caption" className="bold500">Project link</Typography></Box>
                    <Box component="div" mb={2} py={0.5} px={0.5} className="projectLink" display="flex" alignItems="center" justifyContent="space-between">
                        <InputBase placeholder="Project Link" color="primary" defaultValue="https//:app.scrumbooster.team/project 1"/>
                        <IconButton type="button" aria-label="search" ><FileCopyOutlinedIcon /><Link underline="none" style={{ color: '#2979FF' }}>Copy Link</Link></IconButton>
                    </Box>
                </Box>
                <Box>
                    <Box component="div" mb={1}><Typography variant="caption" className="bold500">Recipients</Typography></Box>
                    <Box className="recipients-container">
                        <Chip size="small" label="steave.waugh@abc.com" onDelete={handleDelete} deleteIcon={<CloseIcon />} />
                        <Chip size="small" label="john.miller@abc.com" onDelete={handleDelete} deleteIcon={<CloseIcon />} />
                        <Chip size="small" label="derek.barnhouse@abc.com" onDelete={handleDelete} deleteIcon={<CloseIcon />} />
                    </Box>
                </Box>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleClose} color="primary" variant="outlined">Cancel</Button>
                <Button onClick={handleClose} color="primary" variant="contained" autoFocus>Share</Button>
            </DialogActions>
        </Dialog >
    )
}

export default Share;