import React, { useState, useEffect } from 'react';
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Link from '@material-ui/core/Link';
import Select from '@material-ui/core/Select';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import ShareOutlinedIcon from '@material-ui/icons/ShareOutlined';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import GitHubIcon from '@material-ui/icons/GitHub';
import PrintIcon from '@material-ui/icons/Print';
import CheckIcon from '@material-ui/icons/Check';
import DescriptionOutlinedIcon from '@material-ui/icons/DescriptionOutlined';
import Avatar from '@material-ui/core/Avatar';
import AvatarGroup from '@material-ui/lab/AvatarGroup';
import Divider from '@material-ui/core/Divider';
import Datepicker from '../Datepicker';
import Tooltip from '@material-ui/core/Tooltip';
import environment from '../../config/environment';
import './Backlog.scss';
import useStyles from '../Style';
import { useHistory } from 'react-router-dom';

import FormLabel from '@material-ui/core/FormLabel';

const axios = require('axios');

function Backlog(props) {
    const classes = useStyles();
    const history = useHistory();
    const [anchorElMember, setAnchorElMember] = React.useState(null);
    const openMember = Boolean(anchorElMember);
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [option, changeOption] = useState(10);
    const [sprintOption, changeSprintOption] = useState("");

    const handleCloseMember = () => {
        setAnchorElMember(null);
    };
    const openTeamMember = (event) => {
        setAnchorElMember(event.currentTarget);
    };
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };
    const handleChange = (event) => {
        changeOption(event.target.value);
    };
    const handleSprintChange = (event) => {
        changeSprintOption(event.target.value);
    };

    const handleRedirectDashboard = () => {
        history.push("/")
    }

    useEffect(() => {
        console.log('backlog check user info', props.token)
        if (props.token) {
            axios.post(environment.url + '/user/info', { token: props.token })
                .then(response => {
                    if (response.status === 200) {
                        props.checkLoggedIn(true);
                        props.updateUser(response.data.data.user);
                        if (response.data.data.user.steps <= 2) {
                            props.updateStep(response.data.data.user.steps)
                            history.push("/onboarding");
                        }
                    }
                })
                .catch((error) => {
                    props.checkLoggedIn(false);
                    history.push('/login');
                });
        } else {
            history.push('/login')
        }
    }, [props.token]);
    return (
        <React.Fragment>

            <Container maxWidth={"xl"} className="sprint-container">
                <Box display="flex" flexDirection="row">
                    <Box display="flex" className="project-board">
                        <Box className="project-board__left">
                            {/* <Breadcrumbs aria-label="breadcrumb">
                                <Link color="inherit" onClick={handleRedirectDashboard}>Dashboard</Link>
                                <Link color="inherit" href="/getting-started/installation/" onClick={handleClick}>Product Backlog</Link>
                                <Typography color="textPrimary">Product Backlog</Typography>
                            </Breadcrumbs> */}
                            <Breadcrumbs aria-label="breadcrumb">
                                <Link color="inherit" onClick={handleRedirectDashboard} >Dashboard</Link>
                                <Typography color="textPrimary">Product Backlog</Typography>
                            </Breadcrumbs>
                            <Box elevation={0} display="flex" className="repo">
                                <Box component="span" className="ttl has-background" >
                                    <Typography variant="body2" noWrap={false}>Intelligaia</Typography>
                                </Box>
                                <Box elevation={0} display="flex" flexDirection="row" alignItems="center" className="repo__name connected">
                                    <Select
                                        labelId="demo-simple-select-outlined-label"
                                        id="demo-simple-select-outlined"
                                        value={option}
                                        onChange={handleChange}
                                        label="Age"
                                    >
                                        <MenuItem value={10}><GitHubIcon /></MenuItem>
                                        <MenuItem value={20} disabled><img src="/images/gitlab.svg" /></MenuItem>
                                        <MenuItem value={30} disabled><img src="/images/jira.svg" /></MenuItem>
                                    </Select>
                                    <Link href="#" className="g-name d-none">Connect To Github</Link>
                                    <Link href="#" className="g-name" >Add Repository from GIT</Link>
                                </Box>
                                <Box elevation={0} display="flex" flexDirection="row" alignItems="center" className="repo__utility">
                                    <Tooltip title="Share" placement="top" arrow>
                                        <IconButton aria-label="Share" size="small" >
                                            <ShareOutlinedIcon fontSize="small" />
                                        </IconButton>
                                    </Tooltip>
                                    <Tooltip title="More Actions" placement="top" arrow>
                                        <IconButton aria-label="Action" size="small" aria-controls="actionMenu" aria-haspopup="true" onClick={handleClick} >
                                            <MoreVertIcon fontSize="small" />
                                        </IconButton>
                                    </Tooltip>
                                    <Menu
                                        id="actionMenu"
                                        anchorEl={anchorEl}
                                        keepMounted
                                        open={Boolean(anchorEl)}
                                        onClose={handleClose}
                                    >
                                        <MenuItem onClick={handleClose}>Manage Team Members</MenuItem>
                                        <Divider />
                                        <MenuItem onClick={handleClose} disabled>Remove Repository</MenuItem>
                                        <MenuItem onClick={handleClose}><Box className="textDanger" component="span">Delete Project</Box></MenuItem>
                                    </Menu>

                                </Box>
                            </Box>
                            <Box>
                            </Box>
                            <Box elevation={0} className="avatar d-none">
                                <AvatarGroup max={3}>
                                    <Avatar alt="Remy Sharp" src="https://material-ui.com/static/images/avatar/1.jpg" />
                                    <Avatar alt="Travis Howard" src="https://material-ui.com/static/images/avatar/2.jpg" />
                                    <Avatar alt="Cindy Baker" src="https://material-ui.com/static/images/avatar/3.jpg" />
                                </AvatarGroup>
                                <Link href="#" className="g-team" onClick={openTeamMember}>(3) Team Members</Link>
                                <Menu
                                    id="teamMemberMenu"
                                    anchorEl={anchorElMember}
                                    keepMounted
                                    open={Boolean(openMember)}
                                    onClose={handleCloseMember}
                                >
                                    <MenuItem><Box className="MuiTypography-h6">Team Members (3)</Box></MenuItem>
                                    <MenuItem onClick={handleCloseMember}>
                                        <Box display="block">
                                            <Typography variant="subtitle2">Daniel Webster</Typography>
                                            <Box className="MuiTypography-caption">danial@intelligaia.com</Box>
                                        </Box>
                                    </MenuItem>
                                    <Divider />
                                    <MenuItem onClick={handleCloseMember}>
                                        <Box display="block">
                                            <Typography variant="subtitle2">Derek Barnhouse</Typography>
                                            <Box className="MuiTypography-caption">derek@intelligaia.com</Box>
                                        </Box>
                                    </MenuItem>
                                    <Divider />
                                    <MenuItem onClick={handleCloseMember}>
                                        <Box display="block">
                                            <Typography variant="subtitle2">John Miller</Typography>
                                            <Box className="MuiTypography-caption">jmiller@intelligaia.com</Box>
                                        </Box>
                                    </MenuItem>
                                </Menu>
                            </Box>
                        </Box>
                        <Box className="project-board__rgt" flexGrow={1}>
                            <Box display="flex" className="sprint">
                                <Box className="timeline active sprint__backlog" style={{ width: '25%' }}>
                                    <Typography variant="caption" className="sprint__label bold600">Product Backlog</Typography>
                                    <br></br>
                                    <Typography variant="subtitle2" className="sprint__label bold500">0</Typography>
                                </Box>
                                <Box className="timeline active sprint__plan" style={{ width: '30%' }}>
                                    <Typography variant="caption" className="sprint__label bold600">Plan Sprint</Typography>
                                    <Box className="sprint-form">
                                        <FormControl >
                                            <Select value={sprintOption} onChange={handleSprintChange} displayEmpty value={''} inputProps={{ 'aria-label': 'Without label' }} >
                                                <MenuItem value="">
                                                    <em>Select Sprint</em>
                                                </MenuItem>
                                                <MenuItem value={10} className={(sprintOption !== 10 && sprintOption !== "") ? classes.hasCheck : ""}> {sprintOption === 10 ? <CheckIcon></CheckIcon> : <></>}1-Week Sprint</MenuItem>
                                                <MenuItem value={20} className={(sprintOption !== 20 && sprintOption !== "") ? classes.hasCheck : ""}> {sprintOption === 20 ? <CheckIcon></CheckIcon> : <></>}2-Week Sprint</MenuItem>
                                                <MenuItem value={30} className={(sprintOption !== 30 && sprintOption !== "") ? classes.hasCheck : ""}> {sprintOption === 30 ? <CheckIcon></CheckIcon> : <></>}4-Week Sprint</MenuItem>
                                            </Select>
                                        </FormControl>
                                        <Datepicker />
                                        <Button variant="contained" size="small" color="primary" href="#contained-buttons">Go</Button>
                                    </Box>
                                </Box>
                                <Box className="timeline sprint__scrum" style={{ width: '15%' }}>
                                    <Typography variant="caption" className="sprint__label bold600">Daily Scrum</Typography>
                                </Box>
                                <Box className="timeline sprint__review" style={{ width: '15%' }}>
                                    <Typography variant="caption" className="sprint__label bold600">Sprint Review</Typography>
                                </Box>
                                <Box className="timeline sprint__retro" style={{ width: '15%' }}>
                                    <Typography variant="caption" className="sprint__label bold600">Sprint Retrospective</Typography>
                                </Box>
                            </Box>
                        </Box>
                    </Box>
                </Box>
            </Container>
            <Container maxWidth={"xl"} className="product-container">
                <Box display="flex" width="100%" className="backlog">
                    <Box pt={1} pr={2.5} flexGrow={1} className="backlog__left">
                        {/* <Box p={1} flexGrow={1} className="backlog__left"> */}
                        <Box display="flex" alignItems="center">
                            <Box flexGrow={1} className="backlog__title">
                                <Typography variant="subtitle2">Product Backlog (0)</Typography>
                            </Box>
                            <Box className="action-btns" mr={-1}>

                                <Tooltip title="Share" placement="top" arrow>
                                    <IconButton aria-label="Share">
                                        <ShareOutlinedIcon fontSize="small"></ShareOutlinedIcon>
                                    </IconButton>
                                </Tooltip>
                                <Tooltip title="Print" placement="top" arrow>
                                    <IconButton aria-label="Print">
                                        <PrintIcon fontSize="small"></PrintIcon>
                                    </IconButton>
                                </Tooltip>
                                <Tooltip title="More Actions" placement="top" arrow>
                                    <IconButton aria-label="Show More">
                                        <MoreVertIcon fontSize="small"></MoreVertIcon>
                                    </IconButton>
                                </Tooltip>

                            </Box>
                        </Box>
                        <Box display="flex" width="100%" className="userStory">
                            <TextField
                                id="standard-multiline-flexible"
                                label=""
                                multiline
                                rowsMax={20}
                                style={{ width: "100%", paddingLeft: "0" }}
                                onChange={handleChange}
                                placeholder="| Type your user story, issue here"
                            />
                        </Box>
                        <Box border={1} display="flex" borderRadius="borderRadius" p={1} mt={1} borderColor="#B4B4BB">
                            <DescriptionOutlinedIcon style={{ fontSize: 18 }} />
                            <Box ml={1}>
                                <Typography variant="subtitle2" color="primary" className="mb-half d-flex">As a User I can be able to add other links in the current scrum note by using # which auto suggests (issue, document, Scrum Note)</Typography>
                                <Box display="flex">
                                    <Typography variant="body2" color="secondary">#1 opened by <strong>Daniel</strong></Typography>
                                    <Button variant="outlined" color="secondary" className="icon-btn-sm-rounded" startIcon={<img src="/images/icon-figma.svg" alt="figma" />} size="small">scrumbooster end-to...</Button>
                                </Box>
                            </Box>
                        </Box> 
                        
                    </Box>
                    <Box p={1} className="backlog__rgt">
                        <Typography variant="subtitle2">Sprint (0)</Typography>
                    </Box>
                </Box>
            </Container>
        </React.Fragment>
    );
}

export default Backlog;