import { useState, useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import IconButton from '@material-ui/core/IconButton';
import Box from '@material-ui/core/Box';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import InputAdornment from '@material-ui/core/InputAdornment';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import VisibilityIcon from '@material-ui/icons/Visibility';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Icon from '@material-ui/core/Icon';
import Tooltip from '@material-ui/core/Tooltip';
import './Login.scss';
import { useHistory } from 'react-router-dom';
import Message from '../../Core/Message';
import FormHelperText from '@material-ui/core/FormHelperText';
import Input from '@material-ui/core/Input';
import environment from '../../../config/environment';
import validation from '../../../config/validation';
import { withStyles, makeStyles } from '@material-ui/core/styles';
const axios = require('axios');
const icons = [{ icon: 'Microsoft', path: '/images/icon-micro.svg', height: "19px", width: "20px" }, { icon: 'Google', path: '/images/icon-google.svg', height: "20px", width: "20px" }, { icon: 'GitHub', path: '/images/icon-github.svg', height: "20px", width: "20px" }];

function Login(props) {
    const history = useHistory();
    const [visible, setVisible] = useState(false);//for password
    const [open, setOpen] = useState(false);
    const [content, setContent] = useState({ type: "success", message: "" });
    const [email, changeEmail] = useState("");
    const [password, changePassword] = useState("");
    const [inputError, changeInputError] = useState({ email: { invalid: false, message: "" }, password: { invalid: false, message: "" } })
    const handleEmail = (event) => {
        changeEmail(event.target.value);
        var error = { ...inputError };
        error.email = { invalid: false, message: "" };
        if (event.target.value.trim() !== "") { changeInputError(error) }
    }
    const handlePassword = (event) => {
        changePassword(event.target.value);
        var error = { ...inputError };
        error.password = { invalid: false, message: "" };
        if (event.target.value !== "") { changeInputError(error) }
    }

    const handleForgotPassword = () =>{
        history.push('/forgot-password');
    }
    const handleLogin = (event) => {
        event.preventDefault();
        var validateResponse = validation({
            email: email,
            password: password
        });

        if (validateResponse.invalid) {
            var errors = validateResponse.errors;
            changeInputError({
                email: { invalid: errors.email.invalid, message: errors.email.message },
                password: { invalid: errors.password.invalid, message: errors.password.message }
            })
        }
        if (email.trim() !== "" && password.trim() !== "" && !validateResponse.invalid) {
            let data = { email: email, password: password }
            axios.post(environment.url + '/user/login', data)
                .then(response => {
                    if (response.status === 200) {
                        props.updateToken(response.data.data.access_token);
                        props.updateUser(response.data.data.user);
                        props.updateStep(response.data.data.user.steps);
                        // sessionStorage.setItem('access_token', response.data.data.access_token);
                        // setTimeout(function () {
                        //     history.push(response.data.data.user.steps > 2 ? '/' : '/onboarding');
                        // }, 1000);
                        setOpen(true);
                        setContent({ type: "success", message: response.data.message });
                        if (response.data.data.user.steps > 2) {
                            history.push("/");
                        } else {
                            history.push("/onboarding");
                        }
                    }
                })
                .catch((error) => {
                    setContent({ type: "error", message: error.response.data.message });
                    setOpen(true);
                });
        }
    }
    const handleSignup = () => {
        history.push('/signup')
    }
    const handleClose = () => {
        setOpen(false);
    }

    const handleVisible = () => {
        setVisible(!visible);
    }
    
    useEffect(() => {
        if (props.token) {
            axios.post(environment.url + '/user/info', { token: props.token })
                .then(response => {
                    if (response.status === 200) {
                        props.checkLoggedIn(true);
                        props.updateUser(response.data.data.user);
                        if (response.data.data.user.steps <= 2) {
                            props.updateStep(response.data.data.user.steps)
                            history.push("/onboarding");
                        } else {
                            history.push("/");
                        }
                    }
                })
                .catch((error) => {
                    props.checkLoggedIn(false);
                    history.push('/login');
                });
        }
    }, [props.token]);
    return (
        <div className="form-container">
            <Container fixed >
                <Grid container spacing={3} maxwidth={"sm"}>
                    <Grid item xs={12} align="center">
                        <Typography variant="h2">Welcome Back</Typography>
                        <Typography variant="body1">Log in to your scrumbooster account</Typography>

                        <Box display="block" component="div" className="loginForm">
                            <form onSubmit={handleLogin}>
                                <FormControl error={inputError.email.invalid}>
                                    <InputLabel shrink htmlFor="email">Email ID</InputLabel>
                                    <Input
                                        id="email"
                                        type="email"
                                        placeholder="Email"
                                        onChange={handleEmail}
                                    />
                                    <FormHelperText className={inputError.email.invalid ? "" : "d-none"} id="component-error-text">{inputError.email.message}</FormHelperText>
                                </FormControl>
                                <FormControl error={inputError.password.invalid}>
                                    <InputLabel shrink htmlFor="password">Password</InputLabel>
                                    <Input
                                        id="password"
                                        type={visible ? "text" : "password"}
                                        placeholder="Password"
                                        value={password}
                                        autoComplete="off"
                                        onChange={handlePassword}
                                        endAdornment={
                                            <InputAdornment position="end">
                                                <Tooltip title={visible ? "Hide Password" : "Show Password"} placement="top-end" arrow>
                                                    <IconButton aria-label="toggle password visibility" onClick={handleVisible} >
                                                        {visible ? <VisibilityIcon /> : <VisibilityOff />}
                                                    </IconButton>
                                                </Tooltip>
                                            </InputAdornment>
                                        }
                                    />
                                    <FormHelperText className={inputError.password.invalid ? "" : "d-none"} id="component-error-text">{inputError.password.message}</FormHelperText>
                                </FormControl>
                                <Box display="flex" justifyContent="space-between" mt={1} alignItems="center">
                                    <FormControlLabel control={<Checkbox name="rememberMe" color="primary" />} label="Remember me" />
                                    <Link href="#" onClick={handleForgotPassword} variant="caption">Forgot Password?</Link>
                                </Box>
                                <Box mt={4} mb={3}>
                                    <Button color="primary" type="submit" variant="contained" fullWidth>Log In</Button>
                                </Box>
                            </form>
                            <Box mt={4} mb={3} mx='auto' className="authorizeWith" display="flex" alignItems="center" justifyContent="center" flexDirection="column">
                                <Box display="flex" alignItems="center" justifyContent="center">
                                    <Typography variant="overline" className="authorizeText">OR AUTHORIZE WITH</Typography>
                                </Box>
                                <Box display="flex" alignItems="center" justifyContent="space-between" className="authorizeBtn" mt={2} mb={3}>
                                    {icons.map((value, index) => {
                                        return (<Button key={index} variant="outlined" startIcon={<Icon><img alt={value.icon} src={value.path} height={value.height} width={value.width} /></Icon>}>{value.icon}</Button>);
                                    })}
                                </Box>
                                <Box display="flex" alignItems="center" justifyContent="center">
                                    <Typography variant="caption">No account? <Link component="button" variant="caption" onClick={handleSignup}>Sign up</Link></Typography>
                                </Box>
                            </Box>
                        </Box>
                    </Grid>
                </Grid>
            </Container>
            <Message open={open} content={content} onClose={handleClose} />
        </div>
    )
}

export default Login;