import React from 'react';
import LandingPage from '../LandingPage';
import Footer from '../Footer';
import Pricing from '../Pricing';
import Dashboard from '../Dashboard';
import PageNotFound from '../Core/PageNotFound';
import environment from '../../config/environment';
import { useHistory } from 'react-router-dom';
import Loading from '../Core/Loading'
const axios = require('axios');

function ShowPage(props) {
    switch (props.tab) {
        case 0:
            return (
                <React.Fragment>
                    <LandingPage />
                    <Footer />
                </React.Fragment>
            );

        case 1:
            return <Pricing />

        case 2:
            return (
                <React.Fragment>
                    <PageNotFound/>
                </React.Fragment>
            );

        default:
            return (
                <Loading/>
            );
    }
}

function Home(props) {
    const [loading, changeLoading] = React.useState(true);
    const history = useHistory();
    React.useEffect(() => {
        if (props.token) {
            axios.post(environment.url + '/user/info', { token: props.token })
                .then(response => {
                    if (response.status === 200) {
                        props.checkLoggedIn(true);
                        props.updateUser(response.data.data.user);
                        if (response.data.data.user.steps <= 2) {
                            props.updateStep(response.data.data.user.steps)
                            history.push("/onboarding");
                        }
                    }
                    changeLoading(false)
                })
                .catch((error) => {
                    props.checkLoggedIn(false);
                    changeLoading(false)
                });
        }else{
            changeLoading(false)
        }
    }, [props.token]);
    
    return (
        <React.Fragment>
            { loading?
                <Loading/>
                :
                (props.loggedIn
                    ?
                    <Dashboard token={props.token} user={props.user}/>
                    :
                    <ShowPage tab={props.tab}/>
                )
            }
        </React.Fragment>
    );
}

export default Home;